import React, { useState, useEffect, useCallback } from "react";
import { AgGridReact } from "ag-grid-react";
import axios from "axios";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
import Button from "@material-ui/core/Button";
import { FormControl,AppBar,Toolbar,Typography,} from "@material-ui/core";
import InputIcon from "@material-ui/icons/Input";

const BaseDeductionManage = () => {

    const [dataList, setDataList] = useState([]);

    useEffect(() => {
        axios
            .get("http://localhost:8282/hr/salary/baseDeductionManage.do",)
            .then(response => {
                console.log(" ", response);
                setDataList(response.data.baseDeductionList);
                console.log(dataList);
            })
            .catch(e => {
                console.log(e);
            });
    }, []);

//   
let list = [];

//   count let count = 0;      
function CellEditingStopped(row) {
    row.data.status = "update"
    list.push(row.data);
    /*   console.log("  ");
  console.log(row.data);
  console.log("list  "+count);
  console.log(list[count].extSalCode);
  console.log(list[count].extSalName);
  console.log(list[count].ratio);
  console.log(list[count].status); */
    //count++;
};

//  
const updateOnClick = event => {

    if (list) {
        console.log("  " + list);
        axios.post(
            "http://localhost:8282/hr/salary/baseDeductionManage.do",
            {
              sendData : list
            },
            {
                headers: {
                    "Content-Type": "application/json"
                }
            },

        );
        alert("  .");
        window.location.reload(true);

    } else 
        alert("   .");
    }
;


  //AG  
  const state = {
    columnDefs: [
      { headerName: " ", field: "deductionCode", },
      { headerName: "", field: "deductionName",},
      { headerName: " ", field: "ratio", editable:true},

    ],
    rowData: dataList,
  };


  return (
    console.log("", dataList),
    (<div>
          <div>
          <AppBar position="relative">
            <Toolbar>
              <Typography variant="h5"></Typography>
            </Toolbar>
          </AppBar>
        </div>
        <div>
        <FormControl style={{ minWidth: "410px" }}></FormControl>
        <FormControl style={{ minWidth: "200px" }}></FormControl>
        </div>

        <br />
        
        <div
          className="ag-theme-balham"
          style={{
            height: "600px",
            width: "600px",
            textAlign:"center"
          }}
        >
         <AgGridReact
            columnDefs={state.columnDefs}
            rowData={state.rowData}
            onCellEditingStopped={CellEditingStopped}
            onGridReady={event => {
              event.api.sizeColumnsToFit();
            }}
          ></AgGridReact>
          <br/>
        </div>
        <FormControl style={{ minWidth: "200px" }}/>
        <FormControl style={{ minWidth: "200px" }}>
           <Button
            variant="contained"  color="primary"  size="large" onClick={updateOnClick}  startIcon={<InputIcon />} >  
           </Button>
      </FormControl>
      </div>
      
    )
  );
};
export default BaseDeductionManage;

