import React from "react";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
import "./Grid.css"

//========================  ======================//

const Grid = ({ data }) => {
    
  const Grid = [
    // 
    {
      headerName: "",
      field: "empCode",
      sortable: true
    },
    { headerName: "", field: "empName" },
    { headerName: "", field: "applyYearMonth" },
    { headerName: "", field: "basicWorkDays" },
    { headerName: "", field: "weekdayWorkDays" },
    { headerName: "", field: "basicWorkHour" },
    { headerName: "", field: "workHour" },
    { headerName: "", field: "overWorkHour" },
    { headerName: "", field: "nightWorkHour" },
    { headerName: "", field: "holidayWorkDays" },
    { headerName: "", field: "holidayWorkHour" },
    { headerName: "", field: "lateDays" },
    { headerName: "", field: "absentDays" },
    { headerName: "", field: "halfHolidays" },
    { headerName: "", field: "holidays" },
    { headerName: "", field: "finalizeStatus" },
    { headerName: "", field: "status", hide: true },    
  ];

  return (
    <>
      <br />     

      <div
        className={"ag-theme-material"}
        skipHeaderOnAutoSize="true"
        enableColResize="true"
        enableSorting="true"
        enableFilter="true"
        enableRangeSelection="true"        
        rowStyle={{ "text-align": "center" }}
        style={{
          height: "600px",
          width: "100%",
          paddingTop: "25px",
        }}
        cellStyle={{ textAlign: "center" }}
      >
        
          <AgGridReact
            columnDefs={Grid}
            rowData={data}            
            defaultColDef= {{ resizable: true }}
            rowSelection="single"            
            getRowStyle={function(param) {
              //
              if (param.node.rowPinned) {
                return { "font-weight": "bold", background: "#dddddd" };
              }
              return { "text-align": "center" };
            }}
            onGridReady={event => {
              event.api.sizeColumnsToFit();
            }}
            // onGridReady={onGridReady}
            // onCellClicked={onCellClicked}
          />
      </div>
    </>
  )
};

export default Grid;
