import React, { useEffect, useState } from 'react';
import { AgGridReact } from "ag-grid-react";
import axios from "axios";

import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";

//=============================== 2020-09-10   ======================================
const FinanceAccount = (props) => {
    const [data, setData] = useState(null); //useEffect        

    //===================== useEffect    ========================================

    //     
    useEffect(() => {
        NormalAccountList() //  
    }, []);

    function NormalAccountList() {
        axios.get("http://localhost:8282/acc/base/getCustomerList")
            .then(response => { setData(response.data); })
            .catch(e => { console.log(e); });
    }
    //=============================== ag_grid  ================================================

    const FinanceAccountGrid = [
        // 
        { headerName: " ", field: "customerCode", },
        { headerName: "", field: "customerName" },
    ];
    //=================================== ag Grid    ===================================
    const onGridReady = params => {
        params.api.sizeColumnsToFit();   //   
    };
    //===================================================  =========================================
    const onCellClicked = (params) => {
        const Rows = params.api.getSelectedRows(); //  Grid row 1   
        props.handleFinanceRow(Rows); //   slipNo     
    }

    return <>
        <div
            className={"ag-theme-material"}
            skipHeaderOnAutoSize="true"
            enableColResize="true"
            enableSorting="true"
            enableFilter="true"
            enableRangeSelection="true"
            rowStyle={{ "text-align": "center" }}
            style={{
                height: "400px",
                width: "100%",
                paddingTop: "8px",
            }}
        ><AgGridReact
                columnDefs={FinanceAccountGrid} //  
                rowData={data} //Reduce   
                rowSelection="single" //     
                onGridReady={onGridReady} //onload   
                getRowStyle={function (param) { return { "text-align": "center" }; }} //body  
                onCellClicked={onCellClicked} //  
            />
        </div>
    </>
}

export default FinanceAccount;