import React from 'react';


import { AgGridReact } from 'ag-grid-react';
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";


import { Button } from "@material-ui/core";


const ProductionPerformance = ({ handleClick, rowData }) => {

  const columnDefs = [
      { headerName: "", field: "workOrderCompletionDate" },
      { headerName: "", field: "workOrderNo" },
      { headerName: "", field: "mpsNo" },
      { headerName: "", field: "contractDetailNo" },
      { headerName: "", field: "itemClassification", editable: true },
      { headerName: "", field: "itemCode" },
      { headerName: "", field: "itemName" },
      { headerName: "", field: "unit" },
      { headerName: "", field: "workOrderAmount"},
      { headerName: "", field: "actualCompletionAmount" },
      { headerName: "", field: "workSuccessRate" }
  ]


return (
<>
 <Button variant={"outlined"} color={"secondary"} onClick={handleClick} >
      
 </Button>

  <div
    className={"ag-theme-material"}
    style={{
      height: "600px",
      width: "100%"
    }}
  >
    <AgGridReact
      columnDefs={columnDefs}
      rowData={rowData}
      rowSelection="single"
      onGridReady={event => {
        event.api.sizeColumnsToFit();
      }}
    />
  </div>
  </>
);
};

export default ProductionPerformance;