import React from "react";
import { AgGridReact } from "ag-grid-react";
import useStyles from "erp/account/Page/AccountLedger/Theme";
import { useSelector } from "react-redux";

//AccountSearch    {code}
const AccountLedgerSearch = () => {
  const classes = useStyles(); //

  const { data } = useSelector(state => state.AccReducer); //DB    
  const { isLoading } = useSelector(state => state.AccReducer); //   LOADING....

  //   
  const AccountLedgerGrid = [
    {
      headerName: "",
      field: "reportingDate",
      sortable: true, //   
    },
    {
      headerName: "",
      field: "leftDebtorPrice",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+""', // 3 ,    
    },
    {
      headerName: "",
      field: "rightCreditsPrice",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+""',
    },
    {
      headerName: "",
      field: "totalPrice",
      valueFormatter:
        ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+""',
    },
  ];

  return (
    <div align="center" className={classes.margin}>
      <div
        className={"ag-theme-material"}
        style={{
          height: "100vh",
          //width: "100%",
          //paddingTop: "25px",
        }}
      >
        {!isLoading ? (
          <AgGridReact
            columnDefs={AccountLedgerGrid}
            rowData={data}
            rowSelection="single"
            getRowStyle={function(param) {
              if (param.node.rowPinned) {
                return { "font-weight": "bold", background: "#CEFBC9" };
              }
              return { "text-align": "center" }; // bady  
            }}
            onGridReady={event => {
              // 
              event.api.sizeColumnsToFit();
            }}
          />
        ) : (
          <h1 align="center">Loading...</h1>
        )}
      </div>
    </div>
  );
};
export default AccountLedgerSearch;
