import React from "react";

// MenuStructure Sidebar import

// map    DB     
//  

import SidebarLink from "common/Sidebar/components/SidebarLink/SidebarLink";

// redux
import { connect } from "react-redux";

// action-saga

const MenuStructure = ({ menuTitle, isSidebarOpened, location }) => {
  if (!menuTitle) {
    return <div>Loading... </div>;
  }

  return (
    <div>
      {menuTitle.map(title => (
        <SidebarLink
          key={title.menuCode}
          location={location}
          isSidebarOpened={isSidebarOpened}
          label={title.menuName}
          {...title}
        />
      ))}
    </div>
  );
};
const mapStateToProps = state => {
  return {
    menuTitle: state.menuListReducer.menuList,
  };
};

export default connect(mapStateToProps)(MenuStructure);
