import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import NewsItems from 'common/dashboard/components/NewsItems';
import axios from 'axios';

//  div  .   
const NewsListBlcok = styled.div` 
    box-sizing: border-box;
    padding-bottom: 3rem;
    width: 768px;
    margin: 0 auto;
    margin-top: 2rem;
    @media screen and (max-width: 768px){
        width:100%;
        padding-left: 1rem;
        padding-right: 1rem;
    }
    `;

const Basic = () => {
    const [articles, setArticle] = useState(null);
    const [loading, setLoading] = useState(false);

    useEffect( () => {
        const fetchData = async () => {
            setLoading(true);
            try {
                const response = await axios.get("http://newsapi.org/v2/top-headlines?country=kr&apiKey=bf9e2148e87c473eb3ceacae8485618f"); //  .
                setArticle(response.data.articles);
                setLoading(false);
                console.log(" ");
            }catch (e){
                console.log(e);
            }
        };
          fetchData();
        },[]); //     .   loadig    

        if(loading) {
            return <NewsListBlcok> ....</NewsListBlcok>
        } //   loading  ,  API      

        if(!articles){
            console.log('articles is null');
            return null;
        } //  articels     Null     .    Map   . 


    return (
        <NewsListBlcok>
            {articles.map(article => (
                <NewsItems key={article.url} article={article} />
            ))}
        </NewsListBlcok>
    );
};

export default Basic;