import React, { useState } from "react";
import { AgGridReact } from "ag-grid-react";

const AccountGrid = ({ accountInfo }) => {
  const data = accountInfo;

  //==========================    ==========================
  const [positionGridApi, setPositionGridApi] = useState();
  const onGridReady = params => {
    setPositionGridApi(params.api);
    params.api.sizeColumnsToFit(); //      .
  }; //   ag Grid api   .   ag Grid   .

  //==========================  ==========================
  const accountColumnDefs = [
    { headerName: "", field: "accountInnerCode", width: 80 },
    { headerName: "", field: "accountName", width: 100 },
    { headerName: "", field: "accountCharacter", width: 80 },
  ];

  return (
    <div
      className={"ag-theme-material"}
      style={{
        height: "100vh",
        width: "100%",
        align: "center",
      }}
    >
      <AgGridReact
        columnDefs={accountColumnDefs}
        rowData={data} // setData state     .
        onGridReady={onGridReady}
      />
    </div>
  );
};

export default AccountGrid;
