import React from 'react';

import { AgGridReact } from 'ag-grid-react';
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";
import { Button } from "@material-ui/core";

import WorkInstructionDialogContainer from 'erp/logistic/Page/WorkInstruction/Container/WorkInstructionDialogContainer';



const WorkInstructions = ({
                            onRowSelected,
                            handleClick,
                            handleRequest,
                            rowData
                          }) => {

  const columnDefs = [
      { headerName: "", checkboxSelection: true },
      { headerName: "", field: "mrpNo" },
      { headerName: "", field: "mpsNo" },
      { headerName: "", field: "mrpGatheringNo" },
      { headerName: "", field: "itemClassification", editable: true },
      { headerName: "", field: "itemCode" },
      { headerName: "", field: "itemName" },
      { headerName: "", field: "unitOfMrp"},
      { headerName: "", field: "requiredAmount" },
      { headerName: "", field: "orderDate" },
      { headerName: "", field: "requiredDate" },
  ];


 
return (
    <>
      <Button variant={"outlined"} color={"secondary"} onClick={handleClick}>
           
      </Button>
      <Button variant={"outlined"} color={"secondary"} onClick={handleRequest}>
          
      </Button>
      <WorkInstructionDialogContainer />
          <div
            className={"ag-theme-material"}
            style={{
              height: "600px",
              width: "100%",
            }}
          >
            <AgGridReact
              columnDefs={columnDefs}
              rowData={rowData}
              rowSelection="single"
              onRowSelected={onRowSelected}
              onGridReady={event => {
                event.api.sizeColumnsToFit();
              }}
            />
         </div>
    </>
);
};



export default WorkInstructions;