import React, { useState } from "react";
import useMonth from "../MonthAttendance/useMonth"; //  util 
import { AgGridReact } from "ag-grid-react";
import Select from "react-select";
import { Button, FormControl, AppBar, Toolbar, Typography } from "@material-ui/core";
import InputIcon from "@material-ui/icons/Input";
import SearchIcon from "@material-ui/icons/Search";
//**************************2020-08-20 63   ********************************* 


//ag    
const CloseSalary = ({
  handleChange,
  salaryList,
  findCloseSalary,
  setEvent,
  closeSalaryWithSlipRequest,
  makeJournal
}) => {

  var [positionGridApi, setPositionGridApi] = useState([]); //    
  const [empCodes, setEmpCodes] = useState();

  //=================================================== ag Grid    =========================================
  const onGridReady = params => {
    setPositionGridApi(params.api);
    params.api.sizeColumnsToFit();   //   
  };


  // 
  const callCloseSalary = () => {

    const empcode1 = positionGridApi.getSelectedRows(); // row  
    if (empcode1.length === 0) {
      alert(" .");
      return;
    } if (!window.confirm("   ? ")) {
      alert("  ");
      return;
    }
    if (empcode1.length !== 0) {
      for (var i = 0; i < empcode1.length; i++) {
        if (empcode1[i].finalizeStatus === "Y") {
          alert("  .");
          return;
        } else {
          empcode1[i].finalizeStatus = "Y";
        }
      }
      closeSalaryWithSlipRequest({ empcode1 });
    }
    console.log("llllllllllllllllllll " + JSON.stringify(empcode1))
    //closeSalaryWithSlipRequest({ empcode1 });
    makeJournal(empcode1);


  };


  const state = {
    columnDefs: [
      {
        headerName: "",
        field: "empCode",
        sortable: true,
        headerCheckboxSelection: true,
        headerCheckboxSelectionFilteredOnly: true,
        checkboxSelection: true,
      },
      { headerName: "", field: "applyYearMonth", hide: true },
      {
        headerName: "", field: "salary",
        valueFormatter: ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" "',
      },
      { headerName: "", field: "cost" },
      { headerName: "", field: "unusedDaySalary" },
      { headerName: "", field: "totalExtSal", valueFormatter: ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" "', },
      { headerName: "", field: "totalPayment", valueFormatter: ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" "', },
      { headerName: "", field: "totalDeduction", valueFormatter: ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" "', },
      { headerName: "", field: "realSalary", valueFormatter: ' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+" "', },
      { headerName: "", field: "finalizeStatus" },
      { headerName: "", field: "deptCode", hide: true },
    ],
    rowSelection: "multiple",
    rowData: salaryList,
  };

  const set1 = useMonth();

  const selectedDeptd = [
    {
      value: "DPT-01",
      label: "",
    },
    {
      value: "DPT-02",
      label: "",
    },
    {
      value: "DPT-03",
      label: "",
    },
  ];

  return (
    <div>
      <div>
        <AppBar position="relative">
          <Toolbar>
            <Typography variant="h5"> </Typography>
          </Toolbar>
        </AppBar>
      </div>
      <br />

      <br />
      <FormControl style={{ minWidth: "300px" }}></FormControl>
      <FormControl style={{ maxWidth: "600px" }}></FormControl>
      <FormControl style={{ minWidth: "250px" }}>
        <Select
          onChange={handleChange}
          name="dept"
          // onInputChange={handleInputChange}
          placeholder=" "
          options={selectedDeptd}
          style={{
            width: "500px",
          }}
        ></Select>
      </FormControl>

      <FormControl style={{ Width: "600px" }}> </FormControl>
      <FormControl style={{ minWidth: "250px" }}>
        <Select
          onChange={handleChange}
          name="date"
          // onInputChange={handleInputChange}
          placeholder=" "
          options={set1.selectedmonth}
        />
      </FormControl>
      <Button
        variant="contained"
        color="primary"
        size="large"
        startIcon={<SearchIcon />}
        onClick={findCloseSalary}
      >
        
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        startIcon={<InputIcon />}
        onClick={callCloseSalary}
      >
        
      </Button>
      <br />
      <br />
      <br /> 
      <div
        className="ag-theme-material"
        style={{
          height: "600px",
          width: "100%",
          textAlign: "center"
        }}
      >
        <br />
        <AgGridReact
          suppressRowClickSelection={true}
          columnDefs={state.columnDefs}
          rowData={state.rowData}
          rowSelection={state.rowSelection}
          onGridReady={onGridReady} //onload   
        ></AgGridReact>
      </div>
    </div>
  );
};
export default CloseSalary;
