import React, { useState, useEffect } from "react";
import { createStructuredSelector } from "reselect";
import CloseSalary from "./CloseSalary";
import { connect, useDispatch } from "react-redux";
import {
  salaryListRequest,
  closeSalaryWithSlipRequest,
} from "erp/hr/Action/Action";
import { selectSalaryList, selectedErrorCD } from "../../Selector/selector";
import * as types from "erp/account/ActionType/ActionType";
import { insertSalarySlipStart } from "erp/account/Action/Action";
import useDate from "./useDate";

const CloseSalaryContainer = props => {
  const {
    salaryList,
    salaryListRequest,
    closeSalaryWithSlipRequest,
    flag, //           
  } = props;

  const dispatch = useDispatch();
  const [date, setDate] = useState("");
  const [dept, setDept] = useState("");
  const [empCodes, setEmpCodes] = useState();
  const [event, setEvent] = useState();
  const [journalLists, setJournalLists] = useState([
    //    /    set  .
    {
      journalNo: "NEW JOURNAL", //    .
      slipNo: "NEW",
      balanceDivision: "",
      accountCode: "0603", //  .
      accountName: "",
      customerCode: null,
      leftDebtorPrice: "", // 
      rightCreditsPrice: 0, // 
    },
    {
      journalNo: "NEW JOURNAL",
      slipNo: "NEW",
      balanceDivision: "",
      accountCode: "0101", //  
      accountName: "",
      customerCode: null,
      leftDebtorPrice: 0, // 
      rightCreditsPrice: "", // 
    },
    {
      journalNo: "NEW JOURNAL",
      slipNo: "NEW",
      balanceDivision: "",
      accountCode: "0621", //     
      accountName: "",
      customerCode: null,
      leftDebtorPrice: "", // 
      rightCreditsPrice: 0, // 
    },
    {
      journalNo: "NEW JOURNAL",
      slipNo: "NEW",
      balanceDivision: "",
      accountCode: "0101", //  
      accountName: "",
      customerCode: null,
      leftDebtorPrice: 0, // 
      rightCreditsPrice: "", // 
    },
  ]);

  const [slipData, setSlipData] = useState([
    {
      slipNo: "NEW", //   
      slipType: "",
      accountPeriodNo: "4", //   
      reportingDate: useDate(),
      reportingEmpCode: sessionStorage.getItem("empCodeInfo_token"), // session  empCode
      reportingEmpName: sessionStorage.getItem("empNameInfo_token"),
      slipStatus: "",
      deptCode: sessionStorage.getItem("deptCodeInfo_token"), // session  deptCode
      journalList: [],
    },
  ]);

  // 
  const makeJournal = empcode1 => {
    //       
    const realSalary = empcode1.reduce((stack, el) => {
      return stack + parseInt(el.realSalary);
    }, 0);
    const totalDeduction = empcode1.reduce((stack, el) => {
      return stack + parseInt(el.totalDeduction);
    }, 0);

    const newJournal = journalLists.map((journalState, index) => {
      switch (index) {
        case 0:
          return {
            ...journalState,
            leftDebtorPrice: realSalary,
          };

        case 1:
          return {
            ...journalState,
            rightCreditsPrice: realSalary,
          };

        case 2:
          return {
            ...journalState,
            leftDebtorPrice: totalDeduction,
          };

        case 3:
          return {
            ...journalState,
            rightCreditsPrice: totalDeduction,
          };

        default:
          return { ...journalState };
      }
    });
    const slip = slipData.map(newSlip => {
      return {
        ...newSlip,
        journalList: newJournal,
      };
    });



    slipDataInsert(slip);
  };

  const slipDataInsert = (slip) => {
    props.insertSalarySlipStart({slipData: slip});
  };


  //,   state   
  const handleChange = (event, actionMeta) => {
    if (actionMeta.name === "dept") {
      setDept(event.value);
    } else {
      setDate(event.value);
    }
  };


  //  
  function findCloseSalary() {
    if (dept.length === 0) {
      alert("   ");
      return;
    } else if (date.length === 0) {
      alert("    ");
      return;
    } else {
      salaryListRequest({ date, dept });
    }
  }




  return (
    <div>
      <CloseSalary
        handleChange={handleChange}
        salaryList={salaryList}
        findCloseSalary={findCloseSalary}
        setEvent={setEvent}
        empCodes={empCodes}
        closeSalaryWithSlipRequest={closeSalaryWithSlipRequest}
        makeJournal={makeJournal}
      />
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  salaryList: selectSalaryList,
  flag: selectedErrorCD,
});

export default connect(mapStateToProps, {
  salaryListRequest,
  closeSalaryWithSlipRequest,
  insertSalarySlipStart,
})(CloseSalaryContainer);