//================================== 2020-09-08     =====================================

export default function* AccSaga() {
  yield takeLatest(types.SEARCH_PERIOD_NO_REQUEST, searchPeriodNo);
  yield takeLatest(types.SEARCH_SLIP_REQUEST, searchSlipList);
  yield takeLatest(types.SEARCH_JOURNAL_REQUEST, searchJournalList);
  yield takeLatest(types.SEARCH_ACCOUNT_REQUEST, searchAccountList);
  yield takeLatest(types.ADD_SLIP_REQUEST, addSlip);
  yield takeLatest(types.ADD_SALARY_SLIP_REQUEST, hrAddSlip);
  yield takeLatest(types.SEARCH_AM_SLIP_REQUEST, amSlipRequest);
  yield takeLatest(types.SEARCH_AM_JOURNAL_REQUEST, amJournalRequest);
  yield takeLatest(types.UPDATE_SLIP_REQUEST, updateSlip);
  yield takeLatest(types.SEARCH_JOURNAL_FROM_REQUEST, searchJournalFormList);
  yield takeLatest(types.SEARCH_CUSTOMER_REQUEST, searchCustomerList);
  yield takeEvery(types.SEARCH_FINANCIAL_REQUEST, searchFinancial);
  yield takeEvery(types.SEARCH_TOTALTRIAL_REQUEST, searchTotalTrial);
  yield takeEvery(types.SEARCH_INCOME_REQUEST, searchIncomeList); //<------------ 2020-08-24  
  yield takeLatest(types.SEARCH_CASHJOURNAL_REQUEST, searchCashJournalList); //*********** 2020-08-24   **********
  yield takeLatest(types.SEARCH_DETAILTRIAL_REQUEST, searchDetailTrial); // () 2020-08-24  
  yield takeEvery(types.SEARCH_ACCOUN_TINFO_REQUEST, searchAccountInfo); // <=======  2020-08-25    =============
  yield takeEvery(types.SET_JOURNAL_NO_REQUEST, getJournalNo); // //*********** 2020-08-28   **********
  yield takeEvery(types.BATCH_ACCOUNT_REQUEST, batchCustormerProcess); // <=======  2020-09-01    =============
  yield takeEvery(types.SEARCH_ACCOUNT_LIST_REQUEST, selectAccount); // <=======  2020-09-12    =============
  // <===============  2020-09-10     ================
  yield takeEvery(types.SELECT_SLIP_START, selectSlipSaga); //
  yield takeEvery(types.DELETE_SLIP_START, deleteSlipSaga); //
  yield takeEvery(types.UPDATE_SLIP_START, updateSlipSaga); // 
  yield takeEvery(types.SELECT_JOURNAL_START, searchJournalSaga); //
  yield takeEvery(types.DELETE_JOURNAL_START, deleteJournalSaga); //
  yield takeEvery(types.SAVE_JOURNAL_START, saveJournalSaga); // insert
  yield takeEvery(types.UPDATE_JOURNAL_START, updateJournalSaga); // update
  yield takeEvery(types.SELECT_JOURNAL_DETAIL_START, searchJournalDetailSaga); // 
  yield takeEvery(types.SAVE_JOURNAL_DETAIL_START, saveJournalDetailSaga); //
  // <===============  2020-09-10     ================
}
