import React, { useState, useEffect } from 'react';
import axios from "axios";
import {
    Dialog,
    DialogTitle,
    DialogContent,
    List,
    DialogActions,
    Button,
} from "@material-ui/core";

import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham-dark.css";



const FinancialDialog = ({ open, close, value }) => {
    const [data, setData] = useState(null); //useEffect        


    //==========================    ==========================
    const [positionGridApi, setPositionGridApi] = useState();
    const onGridReady = params => {
        setPositionGridApi(params.api);
        params.api.sizeColumnsToFit();
    };

    //     
    useEffect(() => {
        FinancialList() //  
    }, []);


    function FinancialList() {
        axios.get("http://localhost:8282/acc/base/getDetailCodeList",
            { params: { divisionCodeNo: "CL-02" } }
        ).then(response => {
            setData(response.data.detailCodeList);
        }).catch(e => { console.log(e); });
    }

    //==========================  ==========================
    const accountColumnDefs = [
        { headerName: "", field: "detailCodeName" }
    ];

    //==========================      ==========================
    // onClose  open     Dialog 
    // onClose (data, division)   JournalDetail  .

    //==============================  ====================================
    const Close = () => {
        close({
            data: undefined,
        });
    };
    //==============================    ====================================
    const handleClose = (Rows) => {
        close({ data: Rows.data.detailCodeName });
    };


    return (
        <Dialog aria-labelledby="simple-dialog-title" open={open} fullWidth={true} maxWidth={'xs'}>
            <DialogTitle id="simple-dialog-title" Align="center">  </DialogTitle>
            <DialogContent dividers>
                <List >
                    <div className={"ag-theme-balham"}
                        rowStyle={{ "text-align": "center" }}
                        style={{
                            height: "300px",
                            width: "100%",
                            paddingTop: "8px"
                        }}>
                        <AgGridReact
                            columnDefs={accountColumnDefs}
                            rowData={data}   //  data
                            rowSelection='single'  //   .
                            getRowStyle={function (param) { return { "text-align": "center" }; }} //body  
                            onGridReady={onGridReady}
                            onCellClicked={handleClose}  // cell , handleClose .
                        />
                    </div>
                </List>
            </DialogContent>
            <DialogActions>
                <Button onClick={Close} color="primary">
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
}


export default FinancialDialog;