import React, { useState } from "react";
import { connect } from "react-redux";
import Combine from "./Combine";

import {
  selectSlipStart,
  deleteSlipStart,
  saveJournalStart,
  updateSlipStart, //
  selectJournalStart,
  deleteJournalStart,
  updateJournalStart, //
  searchJournalDetailStart,
  saveJournalDetailStart, //
} from "erp/account/Action/Action";

//=================================================== 2020-09-10    =========================================

const Container = ({
  selectSlipStart,
  slipFormList,
  saveJournalStart,
  updateSlipStart,
  deleteSlipStart, //
  selectJournalStart,
  journalList,
  deleteJournalStart,
  updateJournalStart, //
  searchJournalDetailStart,
  journalDetailList,
  saveJournalDetailStart, //
  error,
}) => {
  const [visibleState, setVisibleState] = useState(true); //  
  const [silpRow, setSilpRow] = useState(); //  Row

  //=======================================================
  const handleSearchSlip = (startDate, endDate, slipStatus) => {
    //console.log(" ->  ");
    selectSlipStart({
      startDate: startDate,
      endDate: endDate,
      slipStatus: slipStatus,
    });
  };
  const handleDeleteSlip = slipNo => {
    //console.log(" ->  " + slipNo);
    deleteSlipStart({ slipNo: slipNo });
  };
  const handleUpdateSlip = (slipType, expenseReport, slipNo) => {
    //console.log("  ->  update " + slipType, expenseReport, slipNo)
    updateSlipStart({
      slipType: slipType,
      expenseReport: expenseReport,
      slipNo: slipNo,
    });
  };
  //=======================================================
  const handleSearchJournal = slipNo => {
    // console.log(" ->  " + slipNo);
    selectJournalStart({ slipNo: slipNo });
  };
  const handleDeleteJournal = (slipNo, journalNo) => {
    // console.log("  ->  " + slipNo, journalNo)
    deleteJournalStart({ slipNo: slipNo, journalNo: journalNo });
  };
  const handleSaveJournal = slipData => {
    //console.log(" ->  insert " + JSON.stringify(slipData));
    saveJournalStart({ slipData: slipData });
  };
  const handleUpdateJournal = updateJournalList => {
    console.log(
      "  ->   update" + JSON.stringify(updateJournalList),
    );
    updateJournalStart({ updateJournalList: updateJournalList });
  };
  //=======================================================
  const handleSearchJournalDetail = journalNo => {
    //console.log("  ->  " + journalNo)
    searchJournalDetailStart({ journalNo: journalNo });
  };
  const handlSaveJournalDetailList = SaveJournalDetailList => {
    //console.log(" ->  " + JSON.stringify(SaveJournalDetailList));
    saveJournalDetailStart({ SaveJournalDetailList: SaveJournalDetailList });
  };
  return (
    <div>
      <Combine
        handleSearchSlip={handleSearchSlip} //
        slipFormList={slipFormList} // 
        handleDeleteSlip={handleDeleteSlip} //
        handleSearchJournal={handleSearchJournal} //
        journalList={journalList} //  
        setVisibleState={setVisibleState} // 
        visibleState={visibleState} // true,false
        setSilpRow={setSilpRow} //List   ( ,      )
        silpRow={silpRow} //  ROW 
        handleDeleteJournal={handleDeleteJournal} // 
        handleSearchJournalDetail={handleSearchJournalDetail} //
        journalDetailList={journalDetailList} //  
        handleUpdateSlip={handleUpdateSlip} // update
        handleSaveJournal={handleSaveJournal} // (1:N) :1,2,3,4
        handlSaveJournalDetailList={handlSaveJournalDetailList}
        handleUpdateJournal={handleUpdateJournal}
      ></Combine>
    </div>
  );
};
const mapStateToProps = state => {
  console.log(
    "  " + JSON.stringify(state.AccReducer.journalList),
  );
  return {
    slipFormList: state.AccReducer.slipFormList,
    journalList: state.AccReducer.journalList,
    error: state.AccReducer.error,
    journalDetailList: state.AccReducer.journalDetailList,
  };
};
export default connect(mapStateToProps, {
  selectSlipStart,
  deleteSlipStart,
  updateSlipStart,
  saveJournalStart,
  selectJournalStart,
  deleteJournalStart,
  updateJournalStart,
  searchJournalDetailStart,
  saveJournalDetailStart,
})(Container);
