import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import List from "@material-ui/core/List";
import { DialogTitle, DialogActions, Dialog, Button } from "@material-ui/core";

import * as types from "erp/account/ActionType/ActionType";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham-dark.css";

import { useDispatch, useSelector } from "react-redux";

import useInput from "util/useInput";

// AccountDialog :   Dialog.
const JournalDetailDialog = ({ journalNo, onClose, open }) => {
  //==========================    ==========================
  const [positionGridApi, setPositionGridApi] = useState();
  const [accountName, setAccountName] = useState("null");

  const dispatch = useDispatch();

  const data = useSelector(
    ({ AccReducer }) => AccReducer.journalDetailList.journalDetailList,
    [],
  );

  const accountCode = useInput("");

  const onGridReady = params => {
    setPositionGridApi(params.api);
  };

  //==========================  ==========================
  const accountColumnDefs = [
    { headerName: "", field: "accountControlName", width: 210 },
    { headerName: "", field: "journalDescription", width: 210 },
  ];

  //==========================   ==========================
  //  Dialog    get     data  data .
  useEffect(() => {
    dispatch({ type: types.SET_JOURNAL_NO_REQUEST, journalNo: journalNo });
  }, [journalNo]);

  const Close = () => {
    onClose();
  };

  return (
    <Dialog
      aria-labelledby="simple-dialog-title"
      open={open}
      fullWidth={true}
      maxWidth={"xs"}
    >
      <DialogTitle id="simple-dialog-title"></DialogTitle>
      <List>
        <div
          className={"ag-theme-balham"}
          style={{
            height: "300px",
            width: "100%",
            paddingTop: "8px",
          }}
        >
          <AgGridReact
            columnDefs={accountColumnDefs}
            rowData={data} //  data
            rowSelection="single" //   .
            onGridReady={onGridReady}
            onCellClicked="" // cell , handleClose .
          />
        </div>
      </List>
      <DialogActions>
        <Button onClick={Close} color="primary">
          Close
        </Button>
      </DialogActions>
    </Dialog>
  );
};
JournalDetailDialog.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};

export default JournalDetailDialog;
