import React from 'react';

import { AgGridReact } from 'ag-grid-react';
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";

import { Button } from "@material-ui/core";

const InstructionStatus = ({ rowData, handleClick }) => {
  
  const columnDefs = [
      { headerName: "" },
      { headerName: "", field: "workOrderNo" },
      { headerName: "", field: "mpsNo" },
      { headerName: "", field: "mrpNo" },
      { headerName: "", field: "mrpGatheringNo", editable: true },
      { headerName: "", field: "itemClassification" },
      { headerName: "", field: "itemCode" },
      { headerName: "", field: "itemName"},
      { headerName: "", field: "unitOfMrp" },
      { headerName: "", field: "requiredAmount" },
      { headerName: "", field: "productionProcessName" },
      { headerName: "", field: "workSiteName" },
      { headerName: "", field: "completionStatus" }
  ]


return (
    <>
        <Button variant={"outlined"} color={"secondary"} onClick={handleClick} >
              
        </Button>
        <Button variant={"outlined"} color={"secondary"} >
              
        </Button>
            <div
            className={"ag-theme-material"}
            style={{
              height: "600px",
              width: "100%",
            }}
          >
            <AgGridReact
              columnDefs={columnDefs}
              rowSelection="single"
              rowData={rowData}
              onGridReady={event => {
                event.api.sizeColumnsToFit();
              }}
            />
          </div>
  </>
);
};

export default InstructionStatus