import React, { useState } from "react";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
import * as types from "erp/account/ActionType/ActionType";
import {
  Toolbar,
  AppBar,
  Typography,
  makeStyles,
  Button,
} from "@material-ui/core";

const SlipGrid = ({ setSlipNo, setFlag }) => {
  const classes = useStyles(); //  

  const data = useSelector(({ AccReducer }) => AccReducer.approvalSlipList);
  const dispatch = useDispatch();

  //==========================    ==========================
  const [positionGridApi, setPositionGridApi] = useState();
  const onGridReady = params => {
    setPositionGridApi(params.api);
    params.api.sizeColumnsToFit(); //      .
  }; //   ag Grid api   .   ag Grid   .

  //==========================  ==========================
  const accountColumnDefs = [
    { headerName: "", field: "check", width: 50, checkboxSelection: true }, // checkboxSelection :  
    { headerName: "", field: "slipNo", width: 150 },
    { headerName: "", field: "accountPeriodNo", width: 100 },
    { headerName: "", field: "slipType", width: 100 },
    { headerName: "", field: "reportingDate", width: 100 },
    { headerName: "", field: "reportingEmpName", width: 100 },
    {
      headerName: "",
      field: "reportingEmpCode",
      width: 100,
      hide: true,
    },
    {
      headerName: "",
      field: "expenseReport",
      width: 180,
      editable: true,
    }, // editable : 
    { headerName: "", field: "approvalDate", width: 100 },
    { headerName: "", field: "approvalEmpCode", width: 100 },
    { headerName: "", field: "slipStatus", width: 80 },
    { headerName: "", field: "deptCode", hide: true },
    { headerName: "status", field: "status", hide: true },
  ];

  //==========================  ==========================
  const initalBtn = () => {
    positionGridApi.selectAll(); //     .
    const allData = positionGridApi.getSelectedRows(); //   .
    positionGridApi.updateRowData({ remove: allData }); //  
  };

  //==========================  ===============================
  const approvalBtn = async () => {
    let selectedData = positionGridApi.getSelectedRows();
    let approvalData = selectedData.map(cv => {
      cv.slipStatus = ""; //         FormControl     true  false      
      cv.approvalDate = moment(new Date()).format("yyyy-MM-DD");
      cv.approvalEmpCode = sessionStorage.getItem("empCodeInfo_token");
      cv.approvalEmpName = sessionStorage.getItem("empNameInfo_token");
      return cv;
    });
    dispatch({
      type: types.UPDATE_SLIP_REQUEST,
      params: { approvalData: approvalData },
    });
    alert(` ${approvalData.length}     . `);
    positionGridApi.updateRowData({ remove: selectedData });
    setFlag(true);
  };

  //==========================  row  ,  ==========================
  const slipChange = () => {
    const rowData = positionGridApi.getSelectedRows(); //  row 
    setSlipNo(rowData[0].slipNo); // row  slipNo . JournalGrid   .
  };

  return (
    <>
      <AppBar color="primary" position="static">
        <Toolbar>
          <Typography variant="h4"></Typography>
          <Typography variant="h6" className={classes.title}></Typography>
          <Button
            className={classes.menuButton}
            variant="contained"
            color="secondary"
            onClick={approvalBtn}
          >
            
          </Button>
          <Button
            className={classes.menuButton}
            variant="contained"
            color="secondary"
            onClick={initalBtn}
          >
            
          </Button>
        </Toolbar>
      </AppBar>
      <div
        className={"ag-theme-material"}
        style={{
          height: "250px",
          width: "100%",
          //paddingTop: "20px",
        }}
      >
        <AgGridReact
          columnDefs={accountColumnDefs}
          rowData={data} // setData state     .
          rowSelection="multiple" //   
          onGridReady={onGridReady}
          onCellClicked={slipChange} //  cell   ,
        />
      </div>
      <br />
    </>
  );
};

//  
const useStyles = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(1),
    height: 330,
    width: 1190,
  },
  subCategory: {
    background: "#232f3e",
    color: "white",
  },
  margin: {
    margin: theme.spacing(1),
  },
  title: {
    flexGrow: 1,
  },
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
}));
export default SlipGrid;
