import React, {useState} from 'react';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    List,
    DialogActions,
    Button,
  } from "@material-ui/core";

import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham-dark.css";


const WorkPlaceCodeDialog = ({open,close,value}) => {

    //==========================    ==========================
    const [positionGridApi, setPositionGridApi] = useState();
    const onGridReady = params => {
        setPositionGridApi(params.api);
    };
    
    
    //==========================  ==========================
    const accountColumnDefs = [
        { headerName: "    ", field: "workplaceCode", width: 210,  },
        { headerName: "   ", field: "workplaceName", width: 210,  }
    ];

    //==========================      ==========================
    // onClose  open     Dialog 
    // onClose (data, division)   JournalGrid  .
      
    const handleClose = () => {
      close({
            data : positionGridApi.getSelectedRows(),  // data  row ,
            division : 'WorkPlaceCodeDialog' 
        });
    };

    const Close = () =>{
        close({
            division : 'WorkPlaceCodeDialog'
        })
    }

    return (
        <Dialog aria-labelledby="simple-dialog-title" open={open} fullWidth={true} maxWidth={'xs'}>
            <DialogTitle id="simple-dialog-title">    </DialogTitle>
            <DialogContent dividers>
            <List >
                <div className={"ag-theme-balham"}
                    style={{
                        height: "300px",
                        width: "100%",
                        paddingTop: "8px"
                    }}>
                    <AgGridReact
                        
                        columnDefs={accountColumnDefs}
                        rowData={value}   //  data
                        rowSelection='single'  //   .
                        onGridReady={onGridReady}
                        onCellClicked={handleClose}  // cell , handleClose .
                    />
                </div>
            </List>
            </DialogContent>
            <DialogActions>
                <Button onClick={Close} color="primary">
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default WorkPlaceCodeDialog;