import React, { useEffect, useState } from 'react';
import GridStyle from './GridStyle';
import useInput from 'util/useInput';
import './App.css';
import CustomizedSelects from './Combo'
import moment from "moment";
import {Paper,TextField,Button,Grid,AppBar,Typography,Toolbar} from "@material-ui/core"
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";

//===========================  / 20-08-27======================//
const DayAttdGrid = (props) =>{

    const empCode = sessionStorage.getItem("empCodeInfo_token");

    const classes = GridStyle();
    //
    let thisTime = moment().format("HH:mm");
     // 
    let today = moment().format("YYYY-MM-DD");
    //
    const [applyDay, setApplyDay] = useState(today);
    //
    const attdType = useInput();
    //
    const attdTypeName = useInput();
    //
    const [time, setTime] = useState(thisTime);

    const [delData, setDelData] = useState([]);

    const [deleteGridList, setDeleteGridList] = useState(null); 
    

    const column = {
      columnDefs: [
        { headerName: "", field: "empCode", hide: true }, // 
        { headerName: "", field: "dayAttdCode", hide: true },
        {
          headerName: "",
          field: "applyDay",
          sortable: true,
          headerCheckboxSelection: true,
          headerCheckboxSelectionFilteredOnly: true,
          checkboxSelection: true,
        }, //
        { headerName: "", field: "attdTypeCode", hide: true },
        { headerName: "", field: "attdTypeName" },
        { headerName: "/", field: "time" },
      ],     
    };

   useEffect(() => {
      console.log(applyDay+"dddddddd"+empCode);
      props.handleDayAttd(applyDay, empCode);
    },[])

    const searchDayAttd = () => {
      props.handleDayAttd(applyDay, empCode);
    }

    const insertDayAttd = () => {
      if(!attdType.value){
        alert('  .')
      }
      props.handleInsertDayAttd(empCode, applyDay, attdType.value, attdTypeName.value, time.replace(":", ""));
      if(!!props.attdData){
        alert(attdTypeName.value+'   .')
      }
    }

    const onRowSelected = (e) => {
        setDelData(
          e.api.getSelectedRows()
          )
    }

    const deleteDayAttd = () => {
      console.log(delData)
      if(delData.length===0){
        alert('   ');
      }else{
      props.deleteDayAttdStart({data: delData});      
      alert('   .');     
      deleteGridList.updateRowData({ remove: delData }); 
    }
    }

    return (
      <React.Fragment>
        <div className="ui primary segment">      
         <AppBar position="static" color="primary">
         <Toolbar>
         <Typography component="h2" variant="h4">
            /          
        </Typography>
         </Toolbar>
         </AppBar>     
      </div>
      <br/><br/>   
      <div> 
        <Grid container spacing={8} alignContent="center" justify="center"  alignItems="baseline">
          <Grid item xs={3}>
            <Paper className={classes.leftPaper}>
              <AppBar position="relative" className={classes.subCategory}>
                <Toolbar>
                  <Typography variant="h5"> </Typography>
                </Toolbar>
              </AppBar>
              <br/><br/><br/><br/>
              <form>
                <TextField
                  id="requestDate"
                  label=""
                  type="date"
                  defaultValue={applyDay}
                  onChange={e=> {
                    setApplyDay(e.target.value);
                  }}
                  className={classes.textField}
                  variant="outlined"
                  InputLabelProps={{
                    shrink: true
                  }}
                />
                <br/><br/>
                <TextField
              variant="outlined"
              id="time"
              label=""
              type="time"
              defaultValue={time}
              color="secondary"
              margin="dense"
              onChange={e => {
                setTime(e.target.value.replace(":", "")); //"0900"
              }}
              className={classes.textField}
              InputLabelProps={{
                shrink: true,
              }}
            ></TextField>
            <br/><br/> 
            <CustomizedSelects className={classes.combo} attdType={attdType} attdTypeName={attdTypeName}></CustomizedSelects>
                <br/><br/>  
            <div align='center'>
            <Button  
            variant="contained" 
            color="primary"
            onClick={searchDayAttd}>
              
            </Button> 
            <Button className={classes.button}
            variant="contained" 
            color="primary"
            onClick={insertDayAttd}>
              
              </Button>
              </div>         
              </form>
            </Paper>
          </Grid>
          <Grid item xs={5} alignContent={"center"}>
            <Paper className={classes.rightPaper}>
              <AppBar position="relative" className={classes.subCategory}>
                <Toolbar>
                  <Typography variant="h5"> </Typography>
                </Toolbar>
              </AppBar>
              <br/>
              <div align='center'>              
              <Button className={classes.button}
               variant="contained" 
               color="primary"
               onClick={deleteDayAttd}
               >
              
              </Button>
              </div>
              <br/>
              <div
                className={"ag-theme-material"}
                skipHeaderOnAutoSize="true"
                enableColResize="true"
                enableSorting="true"
                enableFilter="true"
                enableRangeSelection="true"        
                rowStyle={{ "text-align": "center" }}
                style={{
                  height: "500px",
                  width: "100%"
                }}
                cellStyle={{ textAlign: "center" }}
              >
                <AgGridReact 
                columnDefs={column.columnDefs}
                rowData={props.attdData}
                getRowStyle={function (param) { return { "text-align": "center" }; }}
                onRowSelected={onRowSelected}
                onGridReady={(event) => {
                  event.api.sizeColumnsToFit();
                  setDeleteGridList(event.api);
                }}
                rowSelection="multiple" // 
                 />
              </div>
            </Paper>
          </Grid>
        </Grid>
        </div>
        </React.Fragment>
      );

};

export default DayAttdGrid;