import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import * as types from "erp/account/ActionType/ActionType";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
import { makeStyles, Typography, AppBar, Toolbar } from "@material-ui/core";
import JournalDetailDialog from "./JournalDetailDialog";

const JournalGrid = ({ slipNo, flag }) => {
  // slipNo : SlipGrid   slipNo journal .
  // flag : 3  .

  const data = useSelector(
    ({ AccReducer }) => AccReducer.approvalJournalList,
    [],
  );

  const dispatch = useDispatch();

  //==========================    ==========================
  const [gridApi, setGridApi] = useState();
  const [journalDetailDialogOpen, setJournalDetailDialogOpen] = useState(false);

  const classes = useStyles();

  const onGridReady = params => {
    setGridApi(params.api);
    params.api.sizeColumnsToFit();
  };

  useEffect(() => {
    if (!flag) return;
    initalBtn();
  }, [flag]);
  //==========================  ==========================

  const initalBtn = () => {
    gridApi.selectAll(); //   .
    const allData = gridApi.getSelectedRows(); //    .
    gridApi.updateRowData({ remove: allData }); //   .
  };

  //==========================  ==========================
  useEffect(() => {
    if (slipNo === "" || slipNo === "NEW") return;
    dispatch({
      type: types.SEARCH_AM_JOURNAL_REQUEST,
      params: { slipNo: slipNo },
    });
  }, [slipNo]); // SlipGrid   slipNo   , slipNo    List . setData .

  //==========================  ==========================
  const accountColumnDefs = [
    { headerName: "", field: "check", width: 50, checkboxSelection: true },
    { headerName: "", field: "journalNo", width: 230 },
    { headerName: "", field: "accountCode", width: 100 },
    { headerName: "", field: "accountName", width: 130 },
    { headerName: "", field: "balanceDivision", width: 100 },
    { headerName: "", field: "summaryComment", width: 230 },
    { headerName: "", field: "customerCode", width: 110 },
    { headerName: "", field: "customerName", width: 110 },
    {
      headerName: "",
      field: "price",
      width: 110,
      valueFormatter: currencyFormatter,
    },
    // valueFormatter :  .
    { headerName: "", field: "slipNo", width: 110, hide: true },
    { headerName: "", field: "leftDebtorPrice", width: 110, hide: true },
    { headerName: "", field: "rightCreditsPrice", width: 110, hide: true },
    { headerName: "", field: "summaryNumber", width: 110, hide: true },
  ];

  //==========================   ==========================
  function currencyFormatter(params) {
    return "" + formatNumber(params.value);
  }
  function formatNumber(number) {
    return Math.floor(number)
      .toString()
      .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
  } // .  .

  const onCellClicked = id => {
    // cell    event.
    setJournalDetailDialogOpen(true);
    dispatch({
      type: types.SET_JOURNAL_NO_REQUEST,
      journalNo: id.data.journalNo,
    });
  };

  const handleClose = value => {
    // Dialog   handleClose   . value   .
    setJournalDetailDialogOpen(false);
  };

  return (
    <>
      <AppBar color="inherit" position="static">
        <Toolbar>
          <Typography variant="h4"></Typography>
        </Toolbar>
      </AppBar>
      <div
        className={"ag-theme-material"}
        style={{
          height: "200px",
          width: "100%",
          //paddingTop: "20px",
        }}
      >
        <AgGridReact
          columnDefs={accountColumnDefs}
          rowData={data} //  data .
          rowSelection="multiple" //   .
          onGridReady={onGridReady} //   .
          onCellClicked={onCellClicked} // cell     event
        />
        <JournalDetailDialog
          open={journalDetailDialogOpen}
          onClose={handleClose}
        />
      </div>
    </>
  );
};
const useStyles = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(1),
    height: 330,
    width: "100%",
  },
  subCategory: {
    background: "#232f3e",
    color: "white",
  },
  root: {
    "& > *": {
      margin: theme.spacing(1),
    },
  },
}));
export default JournalGrid;
