import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham-dark.css";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  List,
  DialogActions,
  Button,
  TextField,
} from "@material-ui/core";

const SearchItemCodeDialog = ({ open, close, data}) => {
    

    const [positionGridApi, setPositionGridApi] = useState();
    

    //==========================    ==========================
    const onGridReady = params => {
        setPositionGridApi(params.api);
    };

    //==========================  ==========================
    const accountColumnDefs = [
        { headerName: "", field: "detailCode", width: 180 },
        { headerName: "", field: "detailCodeName", width: 210 },
    ];

    //==========================      ==========================
    // onClose  open     Dialog 
    // onClose (data, division)   AccountSearch  .

    // onclick  handleClose  close 
    const handleClose = () => {
        close({
        data: positionGridApi.getSelectedRows(), // data  row ,
        });
    };

    const Close = () => {
        close({
        });
    };


    return (
        <Dialog
      aria-labelledby="simple-dialog-title"
      open={open}
      fullWidth={true}
      maxWidth={"xs"}
    >
      <DialogTitle id="simple-dialog-title">
        
      </DialogTitle>
      <DialogContent dividers>
        <List>
          <div
            className={"ag-theme-balham"} // 
            style={{
              height: "300px",
              width: "100%",
              paddingTop: "8px",
            }}
          >
            <AgGridReact
              columnDefs={accountColumnDefs} //
              rowData={data} //  data
              rowSelection="single" //   .
              onGridReady={onGridReady}
              onCellClicked={handleClose} // cell , handleClose .
            />
          </div>
        </List>
      </DialogContent>
      <DialogActions>
        <Button onClick={Close} color="primary">
          Close
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default SearchItemCodeDialog;
