import React, { useState, useEffect } from "react";
import useStyles from "erp/account/Page/Slip/Theme";
import { AgGridReact } from "ag-grid-react";
import { Button, AppBar, Toolbar, Typography } from "@material-ui/core";
import FinancialDialog from "erp/account/Page/Slip/Dialog/FinancialDialog";

//=================================================== 2020-09-14    =========================================
const JournalDetail = props => {
  const theme = useStyles(); //CSS
  const [journalDetailData, setJournalDetailData] = useState([]); // 
  const [nodeId, setNodeId] = useState(""); // rowindexid
  const [financialRow, setFinancialRow] = useState(""); //  
  var [openSearchFinancialDialog, setOpenSearchFinancialDialog] = useState(
    false,
  ); // 
  const [datePickerr, setdatePicker] = useState("");

  //=================================================== ag Grid    =========================================
  const onGridReady = params => {
    // console.log(params);
    setJournalDetailData(params.api); //  SET
    params.api.sizeColumnsToFit(); //   
    // params.columnApi.autoSizeColumns([column])
  };
  //=================================================== cell event  =========================================
  const onCellClicked = e => {
    e.api.sizeColumnsToFit(); // 

    //  Dialog 
    if (
      e.colDef.field === "journalDescription" &&
      e.data.accountControlName === ""
    ) {
      setNodeId(e.rowIndex);
      setOpenSearchFinancialDialog(true); //
    }
  };

  //=================================================== Grid  =========================================
  const column = {
    columnDefs: [
      { headerName: "", field: "check", width: 100, checkboxSelection: true },
      { headerName: "", field: "journalDetailNo" },
      { headerName: "", field: "accountControlName" },
      { headerName: "", field: "accountControlType" },
      {
        headerName: "",
        field: "journalDescription",
        editable: true,
        cellEditor: getDatePicker(), //
        cellEditorParams: function(params) {
          //    
          setdatePicker(params.data.accountControlName);
        },
      },
    ],
    //  components: { datePicker: getDatePicker() },
  };

  //ag datePicker
  function getDatePicker(e) {
    if (datePickerr === "") {
      function datePicker() {}
      datePicker.prototype.init = function(paramss) {
        this.eInput = document.createElement("input");
        this.eInput.setAttribute("type", "date");
        this.cell = paramss.eGridCell;
        this.oldWidth = this.cell.style.width;
        this.cell.style.width = "200px";
        this.eInput.value = paramss.value;
      };
      datePicker.prototype.getGui = function() {
        return this.eInput;
      };
      datePicker.prototype.afterGuiAttached = function() {
        this.eInput.focus();
        this.eInput.select();
      };
      datePicker.prototype.getValue = function() {
        this.cell.style.width = this.oldWidth;
        return this.eInput.value;
      };

      return datePicker;
    } else {
      return;
    }
  }

  //===================================================   =========================================
  const saveBtn = () => {
    journalDetailData.stopEditing(); // 
    journalDetailData.selectAll();
    const SaveJournalDetailList = journalDetailData.getSelectedRows(); //   

    if (SaveJournalDetailList.length === 0) {
      alert(" .  .");
    } else if (!window.confirm("   ?")) {
      alert("   ");
      return;
    } else {
      props.handlSaveJournalDetailList(SaveJournalDetailList);
      alert("     ");
    }
  };
  //===================================================   CLOSED  =========================================
  const handleFinancialClose = value => {
    setOpenSearchFinancialDialog(false);
    if (value.data === undefined) {
      return;
    } else {
      setFinancialRow(value.data); // Row set
    }
  };
  //==================================================    ===================================================
  useEffect(() => {
    // accountDialog   row 
    if (financialRow[0] !== undefined) {
      let itemsToUpdate = [];
      // rowNode: rows , index: index
      journalDetailData.forEachNodeAfterFilterAndSort(function(rowNode, index) {
        //  indexId  !==   indexId (    index  insert   )
        if (index !== nodeId) {
          return;
        }
        let FinancialData = rowNode.data; // rowNode : Dialog  data estimateData ,
        FinancialData.journalDescription = financialRow; //
        itemsToUpdate.push(FinancialData); //  ,
      });
      journalDetailData.updateRowData({ update: itemsToUpdate }); //   update . ,  .
    }
  }, [financialRow]); // accountRow     useEffect 
  //=================================================================================================================

  return (
    <>
      <div>
        <FinancialDialog
          open={openSearchFinancialDialog}
          close={handleFinancialClose}
        />
        <div align="right" className={theme.root}>
          <AppBar color="primary" position="static">
            <Toolbar>
              <Typography variant="h4"></Typography>
              <Typography variant="h6" className={theme.title} />
              <Button variant="contained" color="secondary" onClick={saveBtn}>
                 
              </Button>
            </Toolbar>
          </AppBar>
        </div>
        <div
          className={"ag-theme-material"}
          skipHeaderOnAutoSize="true"
          enableColResize="true"
          enableSorting="true"
          enableFilter="true"
          enableRangeSelection="true"
          rowStyle={{ "text-align": "center" }}
          style={{
            height: "200px",
            width: "100%",
            //paddingTop: "25px",
            float: "center",
          }}
          cellStyle={{ textAlign: "center" }}
        >
          <AgGridReact
            columnDefs={column.columnDefs} // 
            rowData={props.journalDetailList} //Reduce  
            rowSelection="single" //     
            onGridReady={onGridReady} //onload   
            getRowStyle={function(param) {
              return { "text-align": "center" };
            }} //body  
            onCellClicked={onCellClicked} //
            //singleClickEdit={true} // 
            onCellEditingStarted={event => {
              console.log("223134214" + event);
              //event.api.sizeColumnsToFit();
            }}
          />
        </div>
      </div>
    </>
  );
};

export default JournalDetail;
