import React, { useState, useEffect } from "react";
import useStyles from "erp/account/Page/Slip/Theme";
import AccountSearchDialog from "erp/account/Page/AccountLedger/AccountSearchDialog";
import CustomerDialog from "erp/account/Page/Slip/Dialog/CustomerDialog";
import { AgGridReact } from "ag-grid-react";
import { Button, AppBar, Toolbar, Typography } from "@material-ui/core";

//=================================================== 2020-09-14    =========================================
const Journal = props => {
  const theme = useStyles();

  var [openAccountSearchDialog, setOpenAccountSearchDialog] = useState(false); // 
  var [openCustomerDialog, setOpenCustomerDialog] = useState(false); // 
  const [journalRow, setJournalRow] = useState(); //
  const [accountRow, setAccountRow] = useState(""); //  
  const [customerRow, setCustomerRow] = useState(""); //  
  const [nodeId, setNodeId] = useState(""); // rowindexid

  //=================================================== Grid  =========================================
  const column = {
    columnDefs: [
      { width: "60", headerCheckboxSelection: true, checkboxSelection: true }, //
      {
        width: "350",
        headerName: "",
        field: "journalNo",
        editable: true,
      },
      { headerName: "", field: "accountCode" },
      { headerName: "", field: "accountName" },
      {
        headerName: "",
        field: "balanceDivision",
        editable: true,
        cellEditor: "agSelectCellEditor", // 
        //List
        cellEditorParams: {
          values: ["", ""],
        },
      },
      { headerName: "", field: "customerCode" },
      { headerName: "", field: "customerName", hide: true },
      {
        headerName: "",
        field: "leftDebtorPrice",
        editable: true,
        //valueFormatter:' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+""',
      },
      {
        headerName: "",
        field: "rightCreditsPrice",
        editable: true,
        //valueFormatter:' Math.floor(value).toString().replace(/(\\d)(?=(\\d{3})+(?!\\d))/g, "$1,")+""',
      },
    ],
  };
  //===================================================   =========================================
  const addBtn = () => {
    const newItem = NewRowData(); // row 
    journalRow.updateRowData({ add: [newItem], addIndex: "" }); // ag api  add 
  };

  //  
  const NewRowData = () => {
    let newData = {
      journalNo: "NEW",
      accountCode: "",
      accountName: "",
      balanceDivision: "",
      customerCode: "",
      customerName: "",
      leftDebtorPrice: "0",
      rightCreditsPrice: "0",
    };
    return newData;
  };
  //===================================================   =========================================
  const deleteBtn = () => {
    const journalRows = journalRow.getSelectedRows(); //    
    if (journalRows.length === 0) {
      alert("  .");
      return;
    } else if (
      props.silpRow[0].slipStatus === "" ||
      props.silpRow[0].slipStatus === ""
    ) {
      alert(" [ " + props.silpRow[0].slipStatus + " ]  :   ");
      return; //   
    } else if (journalRows[0].journalNo === "NEW") {
      journalRow.updateRowData({ remove: journalRows }); //  row 
      return;
    } else if (!window.confirm("  ? ")) {
      alert(" . ");
      return;
    } else {
      const slipNo = journalRows[0].slipNo; // 
      const journalNo = journalRows[0].journalNo; // 
      props.handleDeleteJournal(slipNo, journalNo); // SAGA
      journalRow.updateRowData({ remove: journalRows }); //  row 
      alert("  ");
    }
  };
  //===================================================   =========================================
  const saveBtn = () => {
    journalRow.stopEditing(); // 
    journalRow.selectAll(); //    Read
    const journalRows = journalRow.getSelectedRows(); //   
    const rowsCount = journalRow.getDisplayedRowCount(); //     
    if (rowsCount === 0 || rowsCount === 1) {
      alert("   ");
      return;
    }
    for (var i = 0; i < rowsCount; i++) {
      if (
        journalRows[i].accountCode === null ||
        journalRows[i].accountName === null ||
        journalRows[i].balanceDivision ===
          null /*|| journalRows[i].customerCode === null*/
      ) {
        alert("   . ");
        return;
      }
      //1  row  2 row ?
    }
    if (rowsCount === 1) {
      if (rowsCount !== 2) {
        alert(journalRows[0].balanceDivision + "  ");
        return;
        //  1   1   return
      }
      //1.2  ROW    
    } else if (
      journalRows[0].balanceDivision === journalRows[1].balanceDivision
    ) {
      alert(
        " [" +
          journalRows[0].balanceDivision +
          "]   . .",
      );
      return;
    } else if (
      journalRows[0].leftDebtorPrice !== journalRows[1].rightCreditsPrice
    ) {
      alert(
        " [  : " +
          journalRows[0].leftDebtorPrice +
          " ]  [ : " +
          journalRows[1].rightCreditsPrice +
          " ]  .",
      );
      return;
    } else if (
      journalRows[0].rightCreditsPrice !== journalRows[1].leftDebtorPrice
    ) {
      alert(
        " [  : " +
          journalRows[0].rightCreditsPrice +
          " ]  [ : " +
          journalRows[1].leftDebtorPrice +
          " ]  .",
      );
      return;
      //3  row  4 row ?
    } else if (rowsCount === 3) {
      if (rowsCount !== 4) {
        alert(journalRows[2].balanceDivision + "  ");
        return;
        //  1   1   return
      }
      //3.4  ROW    
    } else if (rowsCount === 4) {
      if (journalRows[2].leftDebtorPrice !== journalRows[3].rightCreditsPrice) {
        alert(
          " [  : " +
            journalRows[2].leftDebtorPrice +
            " ]  [ : " +
            journalRows[3].rightCreditsPrice +
            " ]  .",
        );
        return;
      } else if (
        journalRows[2].rightCreditsPrice !== journalRows[3].leftDebtorPrice
      ) {
        alert(
          " [  : " +
            journalRows[2].rightCreditsPrice +
            " ]  [ : " +
            journalRows[3].leftDebtorPrice +
            " ]  .",
        );
        return;
      } else if (
        journalRows[2].balanceDivision === journalRows[3].balanceDivision
      ) {
        alert(
          " [" +
            journalRows[2].balanceDivision +
            "]   . .",
        );
        return;
      }
      //  or    
    }
    if (
      props.silpRow[0].slipStatus === "" ||
      props.silpRow[0].slipStatus === ""
    ) {
      alert(" [ " + props.silpRow[0].slipStatus + " ]  :   ");
      return;
    } else if (!window.confirm("   ? ")) {
      alert("   ");
      return;
      // ,    insert
    } else if (props.silpRow[0].slipNo === "NEW") {
      console.log("insert");
      let slipData = props.silpRow[0];
      slipData.journalList = journalRows; // { ,:[{1},{2},{3}...]}
      //journalList     ..to/SlipBean  ArrayList<JournalBean> journalList;  (journalList   List )
      //console.log("dddddd " + JSON.stringify(slipData))
      props.handleSaveJournal(slipData); // db
      if (!!props.error) {
        alert("ERROR : " + props.error);
      }
      if (!!props.journalList) {
        alert("   ");
      }

      //,  update
    } else if (props.silpRow[0].slipNo !== "NEW") {
      console.log("update");
      props.handleUpdateJournal(journalRows); // db
      //console.log(":::" + props.journalList)
      if (!!props.error) {
        alert("ERROR : " + props.error);
      }
      if (!!props.journalList) {
        alert("   ");
      }
    }
  };
  //=================================================== cell event  =========================================
  const onCellClicked = e => {
    props.handleSearchJournalDetail(e.data.journalNo); //

    // 
    if (
      props.silpRow[0].slipStatus !== "" ||
      props.silpRow[0].slipStatus !== ""
    ) {
      props.setVisibleState(false);
    }

    //
    if (e.colDef.field === "accountCode" || e.colDef.field === "accountName") {
      setNodeId(e.rowIndex);
      setOpenAccountSearchDialog(true);
      //
    } else if (
      e.colDef.field === "customerCode" ||
      e.colDef.field === "customerName"
    ) {
      setNodeId(e.rowIndex);
      setOpenCustomerDialog(true);
    }
  };

  //===================================================   CLOSED  =========================================
  const handleAccountClose = value => {
    setOpenAccountSearchDialog(false);
    if (value.data === undefined) {
      return;
    } else {
      setAccountRow(value.data); // Row set
    }
  };

  //===================================================   CLOSED  =========================================
  const handleCustomerClose = value => {
    setOpenCustomerDialog(false);
    if (value.data === undefined) {
      return;
    } else {
      console.log(" : " + JSON.stringify(value.data));
      setCustomerRow(value.data); // Row set
    }
  };

  //==================================================    ===================================================
  useEffect(() => {
    // accountDialog   row 
    if (accountRow[0] !== undefined) {
      let itemsToUpdate = [];
      // rowNode: rows , index: index
      journalRow.forEachNodeAfterFilterAndSort(function(rowNode, index) {
        //  indexId  !==   indexId (    index  insert   )
        if (index !== nodeId) {
          return;
        }
        let AccountData = rowNode.data; // rowNode : Dialog  data estimateData ,
        AccountData.accountCode = accountRow[0].accountInnerCode; //
        AccountData.accountName = accountRow[0].accountName; //
        itemsToUpdate.push(AccountData); //  ,
      });
      journalRow.updateRowData({ update: itemsToUpdate }); //   update . ,  .
    }
  }, [accountRow]); // accountRow     useEffect 
  //=================================================================================================================

  //==================================================    ===================================================
  useEffect(() => {
    if (customerRow[0] !== undefined) {
      // customerDialog   row 
      let itemsToUpdate = [];
      // rowNode: rows , index: index
      journalRow.forEachNodeAfterFilterAndSort(function(rowNode, index) {
        //  indexId  !==   indexId (    index  insert   )
        if (index !== nodeId) {
          return;
        }
        let CustomerData = rowNode.data; // rowNode : Dialog  data estimateData ,
        CustomerData.customerCode = customerRow[0].customerCode; //
        CustomerData.customerName = customerRow[0].customerName; //
        itemsToUpdate.push(CustomerData); //  ,
      });
      journalRow.updateRowData({ update: itemsToUpdate }); //   update . ,  .
    }
  }, [customerRow]); // customerRow     useEffect 
  //=================================================================================================================

  return (
    <>
      <div>
        <AccountSearchDialog
          open={openAccountSearchDialog}
          close={handleAccountClose}
        />
        <CustomerDialog open={openCustomerDialog} close={handleCustomerClose} />
        <div align="right" className={theme.root}>
          <AppBar color="primary" position="static">
            <Toolbar>
              <Typography variant="h4"></Typography>
              <Typography variant="h6" className={theme.title} />
              <Button
                className={theme.menuButton}
                variant="contained"
                onClick={addBtn}
                color="secondary"
                disabled={props.visibleState}
              >
                
              </Button>
              <Button
                className={theme.menuButton}
                variant="contained"
                onClick={deleteBtn}
                color="secondary"
              >
                
              </Button>
              <Button
                className={theme.menuButton}
                variant="contained"
                onClick={saveBtn}
                color="secondary"
              >
                
              </Button>
            </Toolbar>
          </AppBar>
        </div>
        <div
          className={"ag-theme-material"}
          skipHeaderOnAutoSize="true"
          enableColResize="true"
          enableSorting="true"
          enableFilter="true"
          enableRangeSelection="true"
          rowStyle={{ "text-align": "center" }}
          style={{
            height: "215px",
            width: "100%",
            //paddingTop: "25px",
            float: "center",
          }}
          cellStyle={{ textAlign: "center" }}
        >
          <AgGridReact
            columnDefs={column.columnDefs} // 
            rowData={props.journalList}
            rowSelection="single" //     
            onGridReady={params => {
              setJournalRow(params.api); //   
              params.api.sizeColumnsToFit();
            }} //onload   
            getRowStyle={function(param) {
              return { "text-align": "center" };
            }} //body  
            onCellClicked={onCellClicked} // 
          />
        </div>
      </div>
    </>
  );
};

export default Journal;
