import React, { useEffect, useState } from "react";
import { AgGridReact } from "ag-grid-react";

import useStyles from "erp/account/Page/AccountLedger/Theme"; // css 

import axios from "axios";
import { useDispatch } from "react-redux";
import * as types from "erp/account/ActionType/ActionType";

import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
import ThreeSixtyIcon from "@material-ui/icons/ThreeSixty";
import DeleteOutlinedIcon from "@material-ui/icons/DeleteOutlined";
import Icon from "@material-ui/core/Icon";
import { Button } from "@material-ui/core";

//=============================== 2020-09-01   ======================================
const NormalAccount = () => {
  const classes = useStyles(); //
  const [data, setData] = useState(null); //useEffect      
  const [positionGridApi, setPositionGridApi] = useState(null); //    
  const dispatch = useDispatch(); //Redux    

  //===================== useEffect    ====================

  //    
  useEffect(() => {
    NormalAccountList(); // 
  }, []);

  function NormalAccountList() {
    axios
      .get("http://localhost:8282/acc/base/getCustomerList")
      .then(response => {
        setData(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  //=============================== ag_grid  ======================================

  const NormalAccountGrid = [
    // 
    { checkboxSelection: true, width: "100", rowSelection: "multiple" },
    {
      headerName: " ",
      field: "customerCode",
      sortable: true,
      width: "240",
    }, // sortable   
    {
      headerName: "",
      field: "customerName",
      editable: true,
      width: "240",
    },
    {
      headerName: " ",
      field: "customerTelNumber",
      editable: true,
      width: "240",
    },
    {
      headerName: " ",
      field: "businessLicenseNumber",
      editable: true,
      width: "240",
    },
    {
      headerName: "",
      field: "customerBusinessConditions",
      editable: true,
      width: "240",
    },
    {
      headerName: "",
      field: "customerBusinessItems",
      editable: true,
      width: "240",
    },
    { headerName: "", field: "status", hide: true, width: "240" },
  ];
  /* 
     customerCode;  /  workplaceCode;  /  customerName;  /  customerType;  /  customerCeo;  /  businessLicenseNumber; 
     / socialSecurityNumber;  / customerBusinessConditions;  / customerBusinessItems;  / customerZipCode;  / customerBasicAddress; 
     customerDetailAddress;  / customerTelNumber;  / customerFaxNumber;  / customerNote;  / accountNumber;  /  cardNumber; 
     cardType;  / cardMemberName;  / cardOpenPlace;  / financialInstituteCode;  / financialInstituteName; 
     */

  //=================================================== ag Grid    =========================================
  const onGridReady = params => {
    setPositionGridApi(params.api); //       

    params.api.sizeColumnsToFit(); //   
  };

  //===================================================  ========================================================

  const addBtn = () => {
    //newCustomerCode()
    const newItem = NewRowData(); // row 
    positionGridApi.updateRowData({ add: [newItem], addIndex: "" }); // ag api  add 
  };
  //  
  const NewRowData = () => {
    positionGridApi.selectAll(); //   
    const rows = positionGridApi.getSelectedRows(); //    
    const lastCode = JSON.stringify(rows[rows.length - 1].customerCode); //  customerCode 
    const subCode = Number(lastCode.substring(5, 7)) + 1; //  +1
    const newCode = "PTN-" + subCode; // 
    console.log(" newCode : " + newCode);

    let newData = {
      customerCode: newCode,
      customerName: "",
      customerTelNumber: "",
      businessLicenseNumber: "",
      customerBusinessConditions: "",
      customerBusinessItems: "",
      status: "insert",
    };
    return newData;
  };

  //===============================================  ============================================================

  ////       .
  const rowCellChanged = e => {
    if (e.data.status === "insert") {
      return;
    } else {
      e.data.status = "update";
    }
  };

  //===============================================  ============================================================

  //  row 
  const deleteBtn = () => {
    const selectedData = positionGridApi.getSelectedRows(); // row

    if (selectedData.length === 0) {
      //  .
      alert(" Row .");
      return;
    }
    if (
      !window.confirm(
        selectedData[0].customerName + "     ? ",
      )
    ) {
      alert(" . ");
      return;
    } else {
      dispatch({
        division: "delete",
        type: types.BATCH_ACCOUNT_REQUEST,
        params: { customerCode: selectedData[0].customerCode },
      });
    }
    positionGridApi.updateRowData({ remove: selectedData }); //  row 
    alert("");
  };

  //===============================================  ============================================================

  const saveBtn = () => {
    positionGridApi.selectAll(); //   
    const rows = positionGridApi.getSelectedRows(); //    
    const rowsCount = positionGridApi.getDisplayedRowCount(); //     

    if (!window.confirm("  ? ")) {
      alert("   ");
      return;
    } else {
      for (var i = 0; i < rowsCount; i++) {
        delete rows[i].errorCode;
        delete rows[i].errorMsg;
        delete rows[i].chk;
        if (rows[i].customerCode.length < 6) {
          alert(" .");
          return;
        }
        console.log("33333333333333  " + JSON.stringify(data));
      }
      dispatch({
        division: "save",
        type: types.BATCH_ACCOUNT_REQUEST,
        params: { customerList: rows },
      });
    }
    alert("  ");
    NormalAccountList(); //
  };

  return (
    <>
      <br />
      <div align="center" className={classes.root3}>
        <Button
          variant="contained"
          color="primary"
          onClick={addBtn}
          startIcon={<Icon className="fa fa-plus-circle" />}
        >
          
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={deleteBtn}
          startIcon={<DeleteOutlinedIcon />}
        >
          
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={saveBtn}
          startIcon={<ThreeSixtyIcon />}
        >
          
        </Button>
      </div>
      <br />
      <br />
      <div
        className={"ag-theme-material"}
        enableSorting="true" //   
        enableFilter="true" //   
        style={{
          height: "550px",
          width: "100%",
          paddingTop: "20px",
          float: "center",
        }}
      >
        <AgGridReact
          columnDefs={NormalAccountGrid} // 
          rowData={data} //Reduce  
          rowSelection="multiple" //     
          onGridReady={onGridReady} //onload   
          getRowStyle={function(param) {
            return { "text-align": "center" };
          }} //body  
          onCellEditingStarted={rowCellChanged} //       .
        />
      </div>
    </>
  );
};

export default NormalAccount;
