import React from 'react';

import { AgGridReact } from 'ag-grid-react';
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";

import { Button } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { makeStyles } from '@material-ui/core/styles';
import ProductionProcessDialogContainer from 'erp/logistic/Page/WorkInstruction/Container/ProductionProcessDialogContainer';
import WISubDialogContainer from 'erp/logistic/Page/WorkInstruction/Container/WISubDialogContainer';

const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: 200,
    },
  },
}));


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const WorkInstructionDialog = ({
                                handleDate,
                                handleRequest,
                                handleActualRequest,
                                hideDialog,
                                subRowData,
                                isOpen,
                                codeName
                                }) => {

    const classes = useStyles();

    const columnDefs = [
        { headerName: "", field: "mrpNo" },
        { headerName: "", field: "mpsNo" },
        { headerName: "", field: "mrpGatheringNo" },
        { headerName: "", field: "itemClassification", editable: true },
        { headerName: "", field: "itemCode" },
        { headerName: "", field: "itemName" },
        { headerName: "", field: "unitOfMrp"},
        { headerName: "()", field: "inputAmount" },
        { headerName: "/", field: "requiredAmount" },
        { headerName: "()", field: "stockAfterWork" },
        { headerName: "", field: "orderDate" },
        { headerName: "", field: "requiredDate" },
    ];

  
    return (
        <Dialog
            fullWidth="lg"
            maxWidth="lg"
            open={isOpen}
            TransitionComponent={Transition}
            keepMounted
            onClose={hideDialog}
            aria-labelledby="alert-dialog-slide-title"
            aria-describedby="alert-dialog-slide-description"
        >
        <DialogTitle id="alert-dialog-slide-title">{"  "}</DialogTitle>
        <form className={classes.root} noValidate autoComplete="off">
            <TextField id="outlined-basic" label="  " variant="outlined" placeholder="  " value={codeName}/>
        </form>
        <DialogActions>
            <Button variant={"outlined"} color={"secondary"} onClick={handleDate}> </Button>
            <Button variant={"outlined"} color={"secondary"} onClick={handleActualRequest}>   </Button>
            <Button variant={"outlined"} color={"secondary"} onClick={handleRequest}>  </Button>
            <ProductionProcessDialogContainer  />
            <WISubDialogContainer />
        </DialogActions>
        <DialogContent>
            <div
                className={"ag-theme-material"}
                style={{
                height: "300px",
                width: "100%",
                }}
                >
                  <AgGridReact
                          columnDefs={columnDefs}
                          rowData={subRowData}
                          rowSelection="single"
                          onGridReady={event => {
                              event.api.sizeColumnsToFit();
                          }}
                  />
            </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={hideDialog} color="primary">
           
          </Button>
        </DialogActions>
      </Dialog>
    )
};




export default WorkInstructionDialog;