import React from "react";
import { AgGridReact } from "ag-grid-react/lib/agGridReact";
import { Button } from "@material-ui/core";

const CtRegister = ({
    basicDataList, DetailDataList,
    searchBtn, onCellClicked, 
    regContracteBtn, cancelContracteBtn
}) => {
    // ag-grid column headers
    const state = {
        ctColumns: [ 
            { headerName: "", field: "chk", width: 40, },
            { headerName: "", field: "estimateNo", width: 150, },
            { headerName: "", field: "contractType", width: 100, },
            { headerName: "", field: "customerCode", width: 100, },
            { headerName: "", field: "estimateDate", width: 150, },
            { headerName: "", field: "contractRequester", width: 100, editable: true, },
            { headerName: "", field: "effectiveDate", width: 150, },
            { headerName: "", field: "personCodeInCharge", width: 130, },
            { headerName: "", field: "description", width: 150, editable: true, },
        ],
        ctDetailColumns: [ 
            { headerName: "", field: "estimateDetailNo", width: 150, },
            { headerName: "", field: "itemCode", width: 100, },
            { headerName: "", field: "itemName", width: 150, },
            { headerName: "", field: "unitOfEstimate", width: 70, },
            { headerName: "", field: "dueDateOfEstimate", width: 150, },
            { headerName: "", field: "estimateAmount", width: 100, },
            { headerName: "", field: "unitPriceOfEstimate", width: 150, },
            { headerName: "", field: "sumPriceOfEstimate", width: 150, },
            { headerName: "", field: "description", width: 150, },
        ],
    };

    return(
        <>  
            <div style={{ marginBottom: '10px', width:'370px', display:"inline-block", }}>
                &nbsp;&nbsp;<Button
                                variant="contained"
                                color="secondary"
                                onClick={searchBtn}
                                ></Button>
                &nbsp;&nbsp;<Button
                                variant="contained"
                                color="secondary"
                                onClick={regContracteBtn}
                            ></Button>
                &nbsp;&nbsp;<Button
                                variant="contained"
                                color="secondary"
                                onClick={cancelContracteBtn}
                                ></Button>
            </div>
            <div className="ag-theme-balham-dark" style={{ height: "200px", width: "100%", paddingTop: "0px" }}>
                <AgGridReact
                    columnDefs={state.ctColumns}
                    rowData={basicDataList}
                    onCellClicked={onCellClicked}
                />
            </div>
            <div className="ag-theme-balham-dark" style={{ height: "250px", width: "100%", paddingTop: "20px" }}>
                <AgGridReact
                    columnDefs={state.ctDetailColumns}
                    rowData={DetailDataList}
                    // onGridReady={}
                    // onSelectionChanged={}
                />
            </div>
        </>
    );
};

export default CtRegister;
