import * as types from "../ActionType/ActionType";

const initialState = {
  isLoading: false,
  slipList: [],
  journalList: [],
  approvalSlipList: [],
  approvalJournalList: [],
  accountList: [],
  customerList: [],
  journalFormList: [],
  error: "",
  periodNo: "",
  journalDetailList: "",
  slipNo: "",
  cashJournalList: [], //********************************** 2020-08-24   **********************************
  detailTrialBalanceList: [], //********************************** 2020-08-24   **********************************
  gridRowJson: [], //********************************** 2020-08-24   **********************************
  IncomeList: [], //********************************** 2020-08-31   **********************************
  slipFormList: [], //==== 2020-09-05   =======
};

const AccReducer = (state = initialState, action) => {
  switch (action.type) {
    //  
    case types.SET_JOURNAL_NO_SUCCESS:
      return {
        ...state,
        journalDetailList: action.data,
      };
    case types.SET_JOURNAL_NO_FAILURE:
      return {
        ...state,
        journalDetailList: action.data,
      };
    case types.SEARCH_PERIOD_NO_SUCCESS:
      return {
        ...state,
        periodNo: action.data,
      };
    case types.SEARCH_PERIOD_NO_FAILURE:
      return {
        ...state,
        error: action.error,
      };

    //  
    case types.SEARCH_SLIP_SUCCESS:
      return {
        ...state,
        slipList: action.data,
      };
    case types.SEARCH_SLIP_FAILURE:
      return {
        ...state,
        error: action.error,
      };

    //  
    case types.SEARCH_JOURNAL_SUCCESS:
      return {
        ...state,
        journalList: action.data.journalList,
      };
    case types.SEARCH_JOURNAL_FAILURE:
      return {
        ...state,
        error: action.error,
      };

    //  
    case types.SEARCH_ACCOUNT_SUCCESS:
      return {
        ...state,
        accountList: action.data,
      };
    case types.SEARCH_ACCOUNT_FAILURE:
      return {
        ...state,
        error: action.error,
      };

    //  
    case types.SEARCH_CUSTOMER_SUCCESS:
      return {
        ...state,
        customerList: action.data,
      };
    case types.SEARCH_CUSTOMER_FAILURE:
      return {
        ...state,
        error: action.error,
      };

    // 
    case types.ADD_SLIP_SUCCESS:
      return {
        ...state,
        slipNo: action.data.slipNo,
      };
    case types.ADD_SLIP_FAILURE:
      return {
        ...state,
        error: action.error,
      };
    case types.ADD_SALARY_SLIP_SUCCESS:
      return {
        ...state,
        slipNo: action.data.slipNo,
      };
    // 
    case types.SEARCH_AM_SLIP_SUCCESS:
      return {
        ...state,
        approvalSlipList: action.data,
      };
    case types.SEARCH_AM_SLIP_FAILURE:
      return {
        ...state,
        error: action.error,
      };
    // 
    case types.SEARCH_AM_JOURNAL_SUCCESS:
      return {
        ...state,
        approvalJournalList: action.data.journalList,
      };
    case types.SEARCH_AM_JOURNAL_FAILURE:
      return {
        ...state,
        error: action.error,
      };
    //   ()
    case types.UPDATE_SLIP_FAILURE:
      return {
        ...state,
        error: action.error,
      };

    //  
    case types.SEARCH_JOURNAL_FROM_SUCCESS:
      return {
        ...state,
        journalFormList: action.data,
      };
    case types.SEARCH_JOURNAL_FROM_FAILURE:
      return {
        ...state,
        error: action.error,
      };
    // 
    case types.SEARCH_FINANCIAL_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case types.SEARCH_FINANCIAL_SUCCESS:
      return {
        ...state,
        isLoading: false,
        data: action.data.financialList,
      };
    case types.SEARCH_FINANCIAL_FAILURE:
      return {
        ...state,
        isLoading: false,
        error: action.error,
      };
    // 
    case types.SEARCH_TOTALTRIAL_REQUEST:
      return {
        ...state,
      };
    case types.SEARCH_TOTALTRIAL_SUCCESS:
      return {
        ...state,
        //data:action.data.financialList,
      };
    case types.SEARCH_TOTALTRIAL_FAILURE:
      return {
        ...state,
        error: action.error,
      };
    //================================ 2020-08-24   ====================================
    case types.SEARCH_INCOME_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case types.SEARCH_INCOME_SUCCESS:
      return {
        ...state,
        isLoading: false,
        IncomeList: action.data.gridRowJson,
      };
    case types.SEARCH_INCOME_FAILURE:
      return {
        ...state,
        isLoading: false,
        error: action.error,
      };
    //================================ 2020-08-24    ====================================
    //********************************** 2020-08-24   **********************************
    //  
    case types.SEARCH_CASHJOURNAL_SUCCESS:
      return {
        ...state,
        cashJournalList: action.data,
      };
    case types.SEARCH_CASHJOURNAL_FAILURE:
      return {
        ...state,
        error: action.error,
      };
    //********************************** 2020-08-24   **********************************
    //********************************** 2020-08-24   **********************************
    //()
    case types.SEARCH_DETAILTRIAL_SUCCESS:
      return {
        ...state,
        detailTrialBalanceList: action.data,
      };
    case types.SEARCH_DETAILTRIAL_FAILURE:
      return {
        ...state,
        error: action.error,
      };
    //********************************** 2020-08-24   **********************************
    //========================================= 2020-08-25     =============================
    //==========  =======
    case types.SEARCH_ACCOUN_LIST_SUCCESS: // 
      return {
        ...state,
        accountList: action.data,
      };
    case types.SEARCH_ACCOUN_LIST_FAILURE: // 
      return {
        ...state,
        error: action.error,
      };
    //==========
    case types.SEARCH_ACCOUN_TINFO_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case types.SEARCH_ACCOUN_TINFO_SUCCESS:
      return {
        ...state,
        isLoading: false,
        data: action.data.gridRowJson,
        accountBean: [],
      };
    case types.SEARCH_ACCOUN_TINFO_FAILURE:
      return {
        ...state,
        isLoading: false,
        error: action.error,
      };
    //========================================= 2020-08-25      ================================
    //========================================= 2020-09-01      =============================

    case types.BATCH_ACCOUNT_FAILURE:
      return {
        ...state,
        error: action.error,
      };
    //========================================= 2020-09-01       ================================
    //========================================= 2020-09-05   ================================
    //========================================
    case types.SELECT_SLIP_START:
      return {
        ...state,
        slipFormList: [], // 
        journalList: [],
        journalDetailList: [],
      };
    case types.SELECT_SLIP_SUCCESS: //
      return {
        ...state,
        slipFormList: action.payload,
        journalList: [], // 
        journalDetailList: [], // 
        accountList: [], //  
      };
    case types.SELECT_SLIP_FAILURE: // 
      return {
        ...state,
        error: action.payload,
      };
    case types.DELETE_SLIP_SUCCESS: // 
      return {
        ...state,
        slipFormList: [], // 
        journalList: [], //  
        journalDetailList: [], //  
      };
    case types.DELETE_SLIP_FAILURE: // 
      return {
        ...state,
        error: action.payload,
      };
    case types.UPDATE_SLIP_FAIL: // UPdate
      return {
        ...state,
        error: action.payload,
      };
    //======================================
    case types.SELECT_JOURNAL_SUCCESS: // 
      return {
        ...state,
        journalList: action.payload.journalList,
        journalDetailList: [], // 
      };

    case types.SELECT_JOURNAL_FAILURE: //
      return {
        ...state,
        error: action.payload,
      };
    case types.DELETE_JOURAL_FAILURE: //
      return {
        ...state,
        error: action.payload,
      };
    case types.UPDATE_JOURNAL_FAILURE: // UPDATE 
      return {
        ...state,
        error: action.payload,
      };
    case types.UPDATE_JOURNAL_SUCCESS: // INSERT 
      console.log("??");
      return {
        ...state,
        journalList: [], // 
        slipFormList: [], // 
        journalDetailList: [], // 
      };
    case types.SAVE_JOURNAL_FAILURE: // INSERT 
      return {
        ...state,
        error: action.payload,
      };
    //======================================
    case types.SELECT_JOURNAL_DETAIL_SUCCESS: //  
      return {
        ...state,
        journalDetailList: action.payload.journalDetailList,
      };
    case types.SELECT_JOURNAL_DETAIL_FAILURE: //  
      return {
        ...state,
        error: action.payload,
      };
    case types.SAVE_JOURNAL_DETAIL_SUCCESS: // 
      return {
        ...state,
        journalDetailList: [], //
      };
    case types.SAVE_JOURNAL_DETAIL_FAILURE: // 
      return {
        ...state,
        error: action.payload,
      };

    default:
      return { ...state };
  }
};

export default AccReducer;
