import React, { useState } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  List,
  DialogActions,
  Button,
  useTheme,
  useMediaQuery,
} from "@material-ui/core";
import { AgGridReact } from "ag-grid-react";
//import "ag-grid-community/dist/styles/ag-grid.css";
//import "ag-grid-community/dist/styles/ag-theme-balham-dark.css";

const CompanyCodeDialog = ({ open, close, value }) => {
  //console.log("value", value);
  //==========================    ==========================
  const [positionGridApi, setPositionGridApi] = useState();
  const onGridReady = params => {
    //console.log("params", params);
    setPositionGridApi(params.api);
  };

  //==========================  ==========================
  const accountColumnDefs = [
    { headerName: "   ", field: "companyCode", width: 210 },
    { headerName: "  ", field: "companyName", width: 210 },
  ];

  //==========================      ==========================
  // onClose  open     Dialog 
  // onClose (data, division)   JournalGrid  .

  const handleClose = () => {
    close({
      data: positionGridApi.getSelectedRows(), // data  row ,
      division: "CompanyCodeDialog",
    });
  };

  const Close = () => {
    close({
      division: "CompanyCodeDialog",
    });
  };

  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));

  return (
    <Dialog
      aria-labelledby="simple-dialog-title"
      open={open}
      fullWidth={true}
      maxWidth={"xs"}
      fullScreen={fullScreen}
    >
      <DialogTitle id="responsive-dialog-title">   </DialogTitle>
      <DialogContent dividers>
        <List>
          <div
            className={"ag-theme-balham"}
            style={{
              height: "300px",
              width: "100%",
              paddingTop: "8px",
            }}
          >
            <AgGridReact
              columnDefs={accountColumnDefs}
              rowData={value} //  data
              rowSelection="single" //   .
              onGridReady={onGridReady}
              onCellClicked={handleClose} // cell , handleClose .
            />
          </div>
        </List>
      </DialogContent>
      <DialogActions>
        <Button onClick={Close} color="primary">
          Close
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default CompanyCodeDialog;
