import React from "react";
import { Dialog, DialogTitle, DialogContent, List, DialogActions, Button } from "@material-ui/core";
import { AgGridReact } from "ag-grid-react/lib/agGridReact";

const LogiModal = ({ isOpenModal, data, closeEsDialog, handleClose }) => {
    const state = {
        commColumnDefs: [ 
            { headerName: "     ", field: "detailCode", width: 150, },
            { headerName: "     ", field: "detailCodeName", width: 150, },
            { headerName: "   ", field: "codeUseCheck", width: 100, }
        ],
        rowData: data,
    };
    
    return(
        <div>
            <Dialog open={isOpenModal} onClose={closeEsDialog} fullWidth={true} maxWidth={'xs'}>
                <DialogTitle id="simple-dialog-title"> </DialogTitle>
                <DialogContent>
                    <List>
                        <div className={"ag-theme-balham"}
                            style={{ height: "300px", width: "100%", paddingTop: "8px" }}>
                            <AgGridReact
                                columnDefs={state.commColumnDefs}
                                rowData={state.rowData}   //  data
                                rowSelection='single'  //   .
                                onCellClicked={handleClose}  // cell , handleClose .
                            />
                        </div>
                    </List>
                </DialogContent>
                <DialogActions>
                    <Button variant="outlined" color="secondary" onClick={closeEsDialog}></Button>
                </DialogActions>
            </Dialog>
        </div>
    );
};
export default LogiModal;