import React, { useState } from "react";
import useInputs from "util/useInputs";
import axios from "axios";

import { AgGridReact } from "ag-grid-react";
import { Button, TextField } from "@material-ui/core";

const OrderRegisterList = ({ setContractNo }) => {
  //    state 
  const [date, onChange] = useInputs({
    fromDate: "",
    toDate: "",
  });
  const { toDate, fromDate } = date;
  const [orderInfo, setOrderInfo] = useState("");

  //  axios   
  const showOrderInfoGrid = () => {
    if (!fromDate && !toDate) {
      alert("  ^^");
      return;
    } else {
      axios
        .get("http://localhost:8282/app/logi/purchase/showOrderInfo.do", {
          params: {
            startDate: fromDate,
            endDate: toDate,
          },
        })
        .then(response => {
          console.log(" ", response);
          setOrderInfo(response.data.gridRowJson);
        })
        .catch(e => {
          console.log(e);
        });
    }
  };

  //  alert
  const state = {
    columnDefs: [
      {
        headerName: "",
        width: 50,
      },
      { headerName: "", field: "orderNo", width: 200 },
      { headerName: "", field: "orderDate", width: 200 },
      { headerName: "", field: "orderInfoStatus", width: 200 },
      { headerName: "", field: "orderSort", width: 200 },
      { headerName: "", field: "itemCode", width: 200 },
      { headerName: "", field: "itemName", width: 200 },
      { headerName: "", field: "itemName", width: 200 },
    ],
    rowData: orderInfo,
  };

  //   
  return (
    <React.Fragment>
      <div className="ui yellow inverted segment">
        {" "}
        <b> [  ] </b>{" "}
      </div>
      <div align="left">
        <TextField
          name="fromDate"
          type={"date"}
          defaultValue={fromDate}
          onChange={onChange}
        />
        &nbsp;&nbsp;&nbsp;&nbsp;
        <TextField
          name="toDate"
          type={"date"}
          defaultValue={toDate}
          onChange={onChange}
        />
        &nbsp;&nbsp;&nbsp;&nbsp;
        <Button
          variant={"outlined"}
          color={"secondary"}
          onClick={showOrderInfoGrid}
        >
          
        </Button>
      </div>
      <br />
      <div>
        <div
          className={"ag-theme-material"}
          style={{
            height: "500px",
            width: "100%",
          }}
        >
          <AgGridReact columnDefs={state.columnDefs} rowData={state.rowData} />
        </div>
      </div>
    </React.Fragment>
  );
};
export default OrderRegisterList;
