import React, { useState } from 'react';
import MpsContractGrid from 'erp/logistic/Page/mps/MpsContractGrid';
import MpsSearchContract from 'erp/logistic/Page/mps/MpsSearchContract';
import MpsGrid from 'erp/logistic/Page/mps/MpsGrid';
import { Button, TextField, makeStyles, Typography, Radio } from "@material-ui/core";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-theme-material.css";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import {FormControlLabel,RadioGroup,} from "@material-ui/core";

import {DatePicker} from '@material-ui/pickers';
import "ag-grid-community/dist/styles/ag-grid.css";
import axios from "axios";

//================================================================2020-09-03  MPS Page  ================================================================= 
//**************************************** 2020.09.06    ****************************************
const useStyles = makeStyles(theme => ({
  btnSearch: {
    fontSize: "0.8rem",
    // background: "black",
    // backgroundColor: "rosybrown",
    // color: "white",
    fontWeight: "bold",
    outline: "none",
    borderRadius: "4px",
    cursor: "pointer",
    border: "none",
  },
  labelStyle: {
    color: "gray",
    padding: theme.spacing(2),
    fontWeight: "bold",
  },
  cal: {
    padding: theme.spacing(2),
  },
  end: {
    padding: theme.spacing(2),
  },
  hrStyle: {
    opacity: "0.4",
  },
  name: {
    color: "dimGray",
    fontWeight: "600",
    fontSize: "20px",
  },
  radio:{
    color: "black",
    fontWeight: "bold",
    outline: "none",
    borderRadius: "4px",
    cursor: "pointer",
    border: "none",
  },
  text:{
    color: "black",
    fontWeight: "bold",
    border: "solid",
    width: "250px",
  },
  dialogText:{
    color: "black",
    fontWeight: "bold",
    border: "solid",
    width: "290px",
    fontSize : "14px",
  },
}));

var DateVal = null;

const gridFrameStyle = {
  height: "300px",
  width: "100%",
  // backgroundColor: "gainsboro",
};

const gridStyle = {
  width: "95%",
  height: "70vh",
  backgroundColor: "whiteSmoke",
};

const MpsRegister = ({ estimateNo, setEstimateNo }) => {

  const classes = useStyles();
  const single = "single";

  const [estimateGridApi, setEstimateGridApi] = useState("");
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [DstartDate, setDstartDate] = useState("");
  const [DendDate, setDendDate] = useState("");
  const [rowData, setRowData] = useState("");
  const [DrowData, setDrowData] = useState("");
  const [DrowData1, setDrowData1] = useState("");
  const [D1rowData, setD1rowData] = useState("");
  const [radioCheck, setRadioCheck] = useState("");
  const [DradioCheck, setDradioCheck] = useState("");
  const [Ddate, setDdate] = useState("");
  const [Ddate1, setDdate1] = useState("");
  const { title }= "";

  function gridReady(params) {
    console.log("----- gridReady()  -----");

    setEstimateGridApi(params.api);
    console.log("estimateGridApi >>>", estimateGridApi);
    return estimateGridApi;
  }
  const startDateChange = e => {
    //  console.log("startDateChange()  - e.target.value -->", e.target.value);

    setStartDate(e.target.value);
  };

  const endDateChange = e => {
    // console.log("endDateChange() - e.target.value -->", e.target.value);

    setEndDate(e.target.value);
  };

  const DstartDateChange = e => {

    setDstartDate(e.target.value);
  };

  const DendDateChange = e => {

    setDendDate(e.target.value);

  };

  const DdateChange = e => {
    setDdate(e.currentTarget.value);
    D1rowData[0].mpsPlanDate= e.currentTarget.value;
    console.log("::::::::::", D1rowData[0].mpsPlanDate);
    console.log(" ::::::::::",e.currentTarget.value);
    setDateOpen(false);
    estimateGridApi.refreshView();
  };
  const DdateChange1 = e => {
    setDdate1(e.currentTarget.value);
    D1rowData[0].scheduledEndDate= e.currentTarget.value;
    console.log("::::::::::", D1rowData[0].scheduledEndDate);
    console.log(" ::::::::::",Ddate1);
    setDateOpen1(false);
    estimateGridApi.refreshView();
  };

  
  const searchContract = e => {// MPS
    console.log("=========MPS======");
    let startd = startDate;
    let endd = endDate;
    if (startd && endd === "") {
      alert("  ");
      return;
    }
    let url = "http://localhost:8282/logi/production/searchMpsInfo.do";

    const getData = async () => 
        await axios({
            method: "POST",
            url: url,
            headers: {
              "content-type": "application/json",
              "Access-Control-Allow-Origin": "*",
            },
            params: {
                startDate: startd,
                endDate: endd,
                includeMrpApply: title
            },
          });

          getData()
          .then(response => {
            console.log(
              "axios  response.data ---> ",
              response.data.gridRowJson,
            );
            setRowData(response.data.gridRowJson);
          })
          .catch(e => {
            console.log("MPS   >> ", e);
          });
  };

  const DsearchContract = e => {//  
    console.log("=============== MPS Dialog ==============");
    let startd = DstartDate;
    let endd = DendDate;
    if (startd && endd === "") {
      alert("  ");
      return;
    }
    let url = "http://localhost:8282/logi/production/searchContractDetailInMpsAvailable.do";
    console.log("   ::::::::::",DradioCheck);
    const getData = async () => 
        await axios({
            method: "POST",
            url: url,
            headers: {
              "content-type": "application/json",
              "Access-Control-Allow-Origin": "*",
            },
            params: {
                searchCondition: DradioCheck,
                startDate: startd,
                endDate: endd,
            },
          });

          getData()
          .then(response => {
            console.log(
              "axios  response.data ---> ",
              response.data.gridRowJson,
            );
            setDrowData(response.data.gridRowJson);
          })
          .catch(e => {
            console.log("MPS   >> ", e);
          });
  };

  const DinsertMPSBtn = e => {//  MPS 
    console.log("=============== MPS Dialog MPS==============");

    let url1 = "http://localhost:8282/logi/production/convertContractDetailToMps.do";
    let MPSVal = JSON.stringify(D1rowData[0]);
    console.log(MPSVal);
    
    
    const batchList = [{
      contractNo : D1rowData[0].contractNo,
      contractType : D1rowData[0].contractType,
      contractDate : D1rowData[0].contractDate,
      customerCode : D1rowData[0].customerCode,
      contractDetailNo : D1rowData[0].contractDetailNo,
      itemCode : D1rowData[0].itemCode,
      itemName : D1rowData[0].itemName,
      unitOfContract : D1rowData[0].unitOfContract,
      estimateAmount : D1rowData[0].estimateAmount,
      stockAmountUse : D1rowData[0].stockAmountUse,
      productionRequirement : D1rowData[0].productionRequirement,
      dueDateOfContract : D1rowData[0].dueDateOfContract,
      description : D1rowData[0].description,
      planClassification : D1rowData[0].planClassification,
      mpsPlanDate : D1rowData[0].mpsPlanDate,
      scheduledEndDate : D1rowData[0].scheduledEndDate,
    }];
    console.log(" MPS  :::::::::",batchList);
    
   // alert("DrowData ::::::",JSON.stringify(D1rowData[0]));
    const getData = async () => 
        await axios.post(url1,{
          contractNo : D1rowData[0].contractNo,
          contractType : D1rowData[0].contractType,
          contractDate : D1rowData[0].contractDate,
          customerCode : D1rowData[0].customerCode,
          contractDetailNo : D1rowData[0].contractDetailNo,
          itemCode : D1rowData[0].itemCode,
          itemName : D1rowData[0].itemName,
          unitOfContract : D1rowData[0].unitOfContract,
          estimateAmount : D1rowData[0].estimateAmount,
          stockAmountUse : D1rowData[0].stockAmountUse,
          productionRequirement : D1rowData[0].productionRequirement,
          dueDateOfContract : D1rowData[0].dueDateOfContract,
          description : D1rowData[0].description,
          planClassification : D1rowData[0].planClassification,
          mpsPlanDate : D1rowData[0].mpsPlanDate,
          scheduledEndDate : D1rowData[0].scheduledEndDate,
          
        });
      //   axios.post(
      //     "http://localhost:8282/logi/sales/addNewEstimates.do",
      //     { estimateDate: today, newEstimateInfo: batchObj,},
      //     { headers: { "Content-Type": "application/json" },}
      // )
          getData()
          .then(response => {
            console.log("MPS ");
            alert("MPS ");

            let startd = DstartDate;
            let endd = DendDate;
            let url = "http://localhost:8282/logi/production/searchContractDetailInMpsAvailable.do";
                    const getData = async () =>  //   
                    await axios({
                        method: "POST",
                        url: url,
                        headers: {
                          "content-type": "application/json",
                          "Access-Control-Allow-Origin": "*",
                        },
                        params: {
                            searchCondition: DradioCheck,
                            startDate: startd,
                            endDate: endd,
                        },
                      });
            
                      getData()
                      .then(response => {
                        console.log(
                          "axios  response.data ---> ",
                          response.data.gridRowJson,
                        );
                        setDrowData(response.data.gridRowJson);
                      })

          })
          .catch(e => {
            console.log("MPS   >> ", e,"::::::",url1);
          });
  };


const [contractDetailOpen, setContractDetailOpen] =useState(false); //MPS 
const [dateOpen, setDateOpen] =useState(false); // 
const [dateOpen1, setDateOpen1] =useState(false); // 

const insertOnclick = () => {
    console.log("  value::::::",radioCheck);
    switch(radioCheck){
        case "contractDetail" : setContractDetailOpen(true);
                                break;
        case "salesPlan" : alert("");
                           break;
        default : alert("   MPS .");
    } 
};
const closeEsDialog =() =>{
    
    if(radioCheck==="contractDetail"){
        setContractDetailOpen(false);
    }else{

    }   
};
const dateClose = (e) =>{
    console.log("123123123123123",e);
  
  setDateOpen(false);
}
const dateClose1 = () =>{
  setDateOpen1(false);
}
const radioOnClick =(e) =>{
    console.log("   >!>!?!?!?!?",e.currentTarget.value);
    setRadioCheck(e.currentTarget.value);
    if(e.currentTarget.value==="contractDetail"){
        console.log(" @@@@@@@@");    
    }else{
        console.log(" @@@@@@@@");
    }
};
const dialogRadioOnClick =(e) =>{
    console.log("    >!>!?!?!?!?",e.currentTarget.value);
    setDradioCheck(e.currentTarget.value);
};    

const onRowClicked = event => {
  console.log("onRowClicked -------- ", D1rowData);
    let list = [];
    list.push(event.api.getSelectedRows());
    setD1rowData(list[0]);
    setDrowData1(event.api.getSelectedRows()[0]);
};
const onCellClicked = e =>{
  console.log("onCellClicked ",e);
  let list = [];
  list.push(e.api.getSelectedRows());
  setD1rowData(list[0]);
  setD1rowData(e.api.getSelectedRows()[0]);
  console.log("123123123",e.api.getSelectedRows()[0]);
  switch(e.colDef.headerName){
      case "" : setDateOpen(true); dataValue(e); break;

      case "":  setDateOpen1(true); break;
  }
}
function dataValue(e){
    console.log("  :::::::::",e);

    if(Ddate != ""){
      console.log("if  ...",Ddate);
      e.value=Ddate;
    }
    console.log("e.value:::::",e.value);
    console.log(" ????????",D1rowData[0]);
    return e.value;
}

const headerName = [
  { headerName: '', checkboxSelection: true, width: 70, headerCheckboxSelection: true },
  { headerName: "", field: "mpsNo", width: 140},
  { headerName: "", field: "mpsPlanClassification", width: 0},
  { headerName: "", field: "contractDetailNo", width: 140 },
  { headerName: "", field: "salesPlanNo", width: 0},
  { headerName: "", field: "itemCode", width: 140 },
  { headerName: "", field: "itemName", width: 140},
  { headerName: "", field: "unitOfMps", minWidth: 70},//hide
  { headerName: "", field: "mpsPlanDate", minWidth: 90 },//hide
  { headerName: "", field: "mpsPlanAmount", width: 140},
  { headerName: "", field: "dueDateOfMps",width: 140, hide: true },
  { headerName: "", field: "scheduledEndDate", width: 140 },
  { headerName: "MRP ", field: "mrpApplyStatus", width: 140, hide: true},
  { headerName: "", field: "description", width: 140, hide: true},
];

const dialogHeaderName = [// dialog
  { headerName: '', checkboxSelection: true, width: 70, headerCheckboxSelection: true },
  { headerName: "", field: "contractDetailNo", width: 140},
  { headerName: "", field: "contractType", width: 140},
  { headerName: "", field: "contractDate", width: 140},
  { headerName: "", field: "estimateAmount", width: 140},
  { headerName: "", field: "stockAmountUse", width: 140},
  { headerName: "", field: "productionRequirement", width: 140},
  { headerName: "", field: "mpsPlanDate", width: 140 ,editable: true,
              onCellValueChanged: function (event) {
                console.log('onCellValueChanged+++++++++++++++++++++++++++++');

                return dataValue
              }   
},
  { headerName: "", field: "scheduledEndDate", width: 140,editable: true},
  { headerName: "", field: "dueDateOfContract", width: 140},
  { headerName: "", field: "customerCode", width: 140},
  { headerName: "", field: "itemCode", width: 140},
  { headerName: "", field: "itemName", width: 140},
  { headerName: "", field: "unitOfContract", width: 140},
  { headerName: "", field: "description", width: 140},
];
  return (
    <>
      <div className="contract_header">
        <div className={classes.cal}>
          <Typography className={classes.labelStyle} onClick={radioOnClick}>&nbsp;&nbsp;&nbsp;
          <TextField
            id={"startDate"}
            type="date"
            value={startDate}
            onChange={startDateChange}
          ></TextField>
          </Typography>

          <Typography className={classes.labelStyle}>&nbsp;&nbsp;&nbsp;
          <TextField
            id={"endDate"}
            type="date"
            value={endDate}
            onChange={endDateChange}
          ></TextField>
          </Typography>
        </div>
        &nbsp;&nbsp;
        <Button
          className={classes.btnSearch}
          variant="contained"
          color="secondary"
          onClick={searchContract}
        >MPS </Button>

      </div>
      <br />
      <hr className={classes.hrStyle} />
      <RadioGroup
            row
            aria-label="position"
            onChange={radioOnClick}
          >
        <fieldset className={classes.text}>
              <legend>
                <strong>MPS    </strong>
              </legend>
              <FormControlLabel
                value="contractDetail"
                control={<Radio />}
                label=""
              />
              <FormControlLabel
                value="salesPlan"
                control={<Radio />}
                label=""
              />
        </fieldset>
      </RadioGroup>
        <br />
        <Button
          className={classes.btnSearch}
          variant="contained"
          color="secondary"
          onClick={insertOnclick}
        > MPS </Button>
        <hr className={classes.hrStyle} />
        <br />
      <div className="contract_body">
        <div
          className={"ag-theme-balham-dark"}
          style={gridFrameStyle}
        >
          <AgGridReact
            onGridReady={gridReady}
            columnDefs={headerName}
            style={gridStyle}
            rowSelection={single}
            rowData={rowData}
          />
        </div>
        <hr className={classes.hrStyle} />
        <br />
      </div>
        <div> 
                <Dialog open={contractDetailOpen} onClose={closeEsDialog} fullWidth={true} maxWidth={'2300px'}>
                    <DialogTitle id="simple-dialog-title">  MPS</DialogTitle>
                    <DialogContent>
                      <RadioGroup aria-label="position" onChange={dialogRadioOnClick}>
                        <fieldset className={classes.dialogText}>
                            <legend>
                                <strong> </strong>
                            </legend>
                            <FormControlLabel
                                value="contractDate"
                                control={<Radio />}
                                label=""
                            />
                            <FormControlLabel
                                value="dueDateOfContract"
                                control={<Radio />}
                                label=""
                            />
                        </fieldset>
                        <fieldset className={classes.dialogText}>
                          <legend>
                              <strong>  </strong>
                          </legend>
                          <TextField
                              id={"DstartDate"}
                              type="date"
                              value={DstartDate}
                              onChange={DstartDateChange}
                          ></TextField>&nbsp;
                          <TextField
                              id={"DendDate"}
                              type="date"
                              value={DendDate}
                              onChange={DendDateChange}
                          ></TextField>&nbsp;
                          <br />
                          <br />
                          <Button
                            className={classes.btnSearch}
                            variant="contained"
                            color="secondary"
                            onClick={DsearchContract}
                          ></Button>
                          <br />
                        </fieldset>
                      <Button
                        className={classes.btnSearch}
                        variant="contained"
                        color="secondary"
                        onClick={DinsertMPSBtn}
                      > MPS</Button>
                      </RadioGroup>
                        <div id="Dgrid" className={"ag-theme-balham-dark"} style={gridFrameStyle}>
                            <AgGridReact
                                onGridReady={gridReady}
                                columnDefs={dialogHeaderName}
                                style={gridStyle}
                                rowSelection='single'
                                rowData={DrowData}
                                setPinnedTopRowData={DrowData}
                                onRowClicked={onRowClicked}
                                onCellClicked={onCellClicked}
                            />
                        </div>
                    </DialogContent>
                    <DialogActions>
                        <Button variant="outlined" color="secondary" onClick={closeEsDialog}></Button>
                    </DialogActions>
                </Dialog>
        </div>
                    {/*   */}
        <div> 
                <Dialog open={dateOpen} onClose={dateClose} maxWidth={'500px'}>
                    <DialogTitle id="simple-dialog-title"> </DialogTitle>
                    <DialogContent>
                        <fieldset>
                            <TextField
                                type="date"
                                onChange={DdateChange}
                            ></TextField>
                        </fieldset>
                    </DialogContent>
                </Dialog>
        </div>
        <div> 
                <Dialog open={dateOpen1} onClose={dateClose1} maxWidth={'500px'}>
                    <DialogTitle id="simple-dialog-title"> </DialogTitle>
                    <DialogContent>
                        <fieldset>
                            <TextField
                                type="date"
                                onChange={DdateChange1}
                            ></TextField>
                        </fieldset>
                    </DialogContent>
                </Dialog>
        </div>
    </>
  );
};

export default MpsRegister;
