import React, { useState } from "react";
import moment from "moment";
import useStyles from "erp/account/Page/Slip/Theme";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
import { Button, AppBar, Toolbar, Typography } from "@material-ui/core";

//========================================== 2020-09-14    =========================================
const SlipForm = props => {
  let toDate = moment(new Date()).format("yyyy-MM-DD");

  const theme = useStyles(); //CSS
  const [slipGrid, setSlipGrid] = useState([]); //  

  //======================================== Grid cell    =========================================
  // []   ,  text 
  const rowCellChanged = params => {
    console.log("slipStatus", params.data.slipStatus);
    if (
      params.data.slipStatus === "" ||
      params.data.slipStatus === ""
    ) {
      alert("  or      ");
      slipGrid.stopEditing(); // 
      return;
    }
  };
  //===================================  Grid row   select ======================================
  const onCellClicked = params => {
    props.setVisibleState(true); //  
    const selectedRows = params.api.getSelectedRows(); //  Grid row 1 
    props.handleSearchJournal(selectedRows[0].slipNo); //   slipNo    
    props.setSilpRow(selectedRows); //  Row

    //  
    if (
      selectedRows[0].slipStatus === "" ||
      selectedRows[0].slipStatus === ""
    ) {
      return;
    }
  };
  //=================================================== Grid =========================================
  const column = {
    columnDefs: [
      { width: "60", headerCheckboxSelection: true, checkboxSelection: true }, //
      { width: "150", headerName: "", field: "accountPeriodNo" },
      { width: "250", headerName: "", field: "slipNo" },
      { headerName: "", field: "reportingDate" },
      { headerName: "", field: "reportingEmpCode" },
      { headerName: "", field: "expenseReport", editable: true }, // editable : 
      { headerName: "", field: "reportingEmpName" },
      { headerName: "", field: "slipStatus" },
    ],
  };
  //===================================================     =========================================
  const addBtn = () => {
    slipGrid.selectAll(); //   
    const rows = slipGrid.getSelectedRows(); //    
    //  1 ( row  if )
    if (rows.length !== 0) {
      if (
        slipGrid.getDisplayedRowAtIndex(slipGrid.getLastDisplayedRow()).data
          .slipNo === "NEW"
      ) {
        alert("  .");
        return;
      }
    }
    const newItem = NewRowData(); // row 
    slipGrid.updateRowData({ add: [newItem], addIndex: "" }); // ag api  add 
  };

  //  
  const NewRowData = () => {
    let newData = {
      slipNo: "NEW",
      accountPeriodNo: "4", //2020  ->  4
      slipType: "",
      reportingDate: toDate,
      reportingEmpCode: sessionStorage.getItem("empCodeInfo_token"), //
      expenseReport: "",
      approvalDate: "",
      reportingEmpName: sessionStorage.getItem("empNameInfo_token"), // 
      slipStatus: "",
      deptCode: "DPT-01",
      status: "",
    };
    return newData;
  };

  //===================================================    ==========================================
  const deleteBtn = () => {
    const deleteList = slipGrid.getSelectedRows();
    if (deleteList.length === 0) {
      alert("  . ");
      return;
    } else if (
      deleteList[0].slipStatus === "" ||
      deleteList[0].slipStatus === ""
    ) {
      alert(
        deleteList[0].slipStatus +
          "      ? ",
      );
      return;
    } else if (!window.confirm("  ? ")) {
      alert(".");
      return;
    } else {
      console.log(":::: :::::" + deleteList[0].slipNo);
      const slipNo = deleteList[0].slipNo; // 
      props.handleDeleteSlip(slipNo);
      slipGrid.updateRowData({ remove: deleteList }); //  row 
      alert("");
    }
  };
  //===================================================    ==========================================
  const saveBtn = () => {
    slipGrid.stopEditing(); // 

    const slipRow = slipGrid.getSelectedRows(); //  row 1
    if (slipRow.length === 0) {
      alert("    ");
      return;
    } else if (
      slipRow[0].slipStatus === "" ||
      slipRow[0].slipStatus === ""
    ) {
      alert(" " + slipRow[0].slipStatus + "     ");
      return;
    } else if (slipRow[0].slipNo !== "NEW") {
      //   db UPDATE 
      props.handleUpdateSlip(
        slipRow[0].slipType,
        slipRow[0].expenseReport,
        slipRow[0].slipNo,
      );

      alert("  . ");
    } else {
      props.setVisibleState(false); //  
      props.setSilpRow(slipRow); //  Row (,   )
      alert("  Button  ");
    }
  };

  return (
    <>
      <div>
        <div className={theme.root}>
          <AppBar color="primary" position="static">
            <Toolbar>
              <Typography variant="h4"></Typography>
              <Typography variant="h6" className={theme.title}></Typography>
              <Button
                className={theme.menuButton}
                variant="contained"
                color="secondary"
                onClick={addBtn}
              >
                
              </Button>
              <Button
                className={theme.menuButton}
                variant="contained"
                color="secondary"
                onClick={deleteBtn}
              >
                
              </Button>
              <Button
                className={theme.menuButton}
                variant="contained"
                color="secondary"
                onClick={saveBtn}
              >
                
              </Button>
            </Toolbar>
          </AppBar>
        </div>

        <div
          className={"ag-theme-material"}
          enableColResize="true" //   
          enableSorting="true" //   
          enableFilter="true" //   
          style={{
            height: "250px",
            width: "100%",
            //paddingTop: "25px",
            float: "center",
          }}
        >
          <AgGridReact
            columnDefs={column.columnDefs} // 
            rowData={props.slipFormList} //Reduce  
            rowSelection="single" //     
            onGridReady={params => {
              setSlipGrid(params.api); //   useState 
              params.api.sizeColumnsToFit();
            }} //onload   
            getRowStyle={function(param) {
              return { "text-align": "center" };
            }} //body  
            onCellEditingStarted={rowCellChanged} //       .
            onCellClicked={onCellClicked} // 
          />
        </div>
      </div>
    </>
  );
};
export default SlipForm;
