import React, { useState } from "react";
import { Button, TextField, makeStyles, Typography, Select, MenuItem, Dialog, DialogTitle, DialogContent, 
  List, DialogActions, Radio, FormControlLabel,RadioGroup} from "@material-ui/core";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/dist/styles/ag-theme-material.css";
import "ag-grid-community/dist/styles/ag-grid.css";
import axios from "axios";
/*#####################################  2020-09-01 #######################################*/
/*###################################### 63  #######################################*/
/*####################################### MRP  #########################################*/
//**************************************** 2020.09.06    ****************************************
const useStyles = makeStyles(theme => ({  
  btnSearch: {
    fontSize: "0.8rem",
    // backgroundColor: "black",
    // color: "white",
    fontWeight: "bold",
    outline: "none",
    borderRadius: "4px",
    cursor: "pointer",
    border: "none",
    width: "20%",
    height: "20%",
  },
  labelStyle: {
    color: "gray",
    padding: theme.spacing(2),
  },
  cal: {
    padding: theme.spacing(2),
  },
  end: {
    padding: theme.spacing(2),
  },
  hrStyle: {
    opacity: "0.4",
  },
  name: {
    fontWeight: "600",
    fontSize: "20px",
    background: "black",
    color: "white",
    width: "100%",
    height: "30px",
  },
  dialog:{
    color: "black",
    fontWeight: "bold",
    border: "solid",
    width: "300px",
  },
  btnDialogSearch: {
    fontSize: "1rem",
    backgroundColor: "black",
    color: "white",
    fontWeight: "bold",
    outline: "none",
    borderRadius: "4px",
    cursor: "pointer",
    border: "none",
    width: "100%",
    height: "20%",
  },
}));

const gridFrameStyle = {
  height: "630px",
  width: "98%",
  backgroundColor: "gainsboro",
};

const gridStyle = {
  width: "98%",
  height: "70vh",
  backgroundColor: "whiteSmoke",
};

const headerName = [
  { headerName: "", field: "mrpNo", width: 200 },
  { headerName: "", field: "mpsNo", width: 170 },  
  { headerName: "", field: "itemClassification", width: 130},
  { headerName: "", field: "itemCode", width: 130},
  { headerName: "", field: "itemName", width: 210 },
  { headerName: "/", field: "orderDate", width: 180 },
  { headerName: "/", field: "requiredDate", width: 180},
  { headerName: "", field: "requiredAmount", width: 130 },
  { headerName: "", field: "unitOfMrp", width: 130 },
];

const mrpGatheringGrid = [
  { headerName: "  ", field: "orderOrProductionStatus", width: 130 },  
  { headerName: "", field: "itemCode", width: 100},
  { headerName: "", field: "itemName", width: 150 },
  { headerName: "", field: "unitOfMrpGathering", width: 50 },
  { headerName: "/", field: "claimDate", width: 140 },
  { headerName: "/", field: "dueDate", width: 140},
  { headerName: "", field: "necessaryAmount", width: 80 },
];

const MrpSearch = () => {
  
  const classes = useStyles();
  const single = "single";
  const [openDialog, setOpenDialog] =useState("");
  const [searchDate,setSearchDate] = useState("");
  const [gridApi, setGridApi] = useState("");
  const [modalGridApi, setModalGridApi] = useState("");
  const [dateVisible,setDateVisible] = useState(false);
  const [rowData, setRowData] = useState("");
  const [dialogData, setDialogData] = useState("");
  const [mrpNoAndItemCodeList, setMrpNoAndItemCodeList]=useState({});
  const [mrpNoList, setMrpNoList]=useState([]);
  

  function gridReady(params) {
    // console.log("----- gridReady()  -----");
    searchMrp();
    setGridApi(params.api);
    //console.log("deliveryGridApi >>>", deliveryGridApi);
    return gridApi;
  }

  function onModaGridReady(params) {
    // console.log("----- gridReady()  -----");
    //console.log(params.api.getRows());
    setModalGridApi(params.api);
    return modalGridApi;
  }
  // console.log("modalGridApi",modalGridApi);
  const searchDateChange = e => {
    setSearchDate(e.target.value);
    // console.log("e.target.value",e.target.value);
  };

  const searchMrp = () => {
    setRowData([]);
    let url = "http://localhost:8282/logi/logistics/production/getMrpList";
    const getData = async () =>
      await axios({
        method: "POST",
        url: url,
        headers: {
          "content-type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
        params : {
                  mrpGatheringStatusCondition : "", 
                  dateSearchCondition :"",
                  mrpStartDate:"",
                  mrpEndDate:"",
                  mrpGatheringNo:""
                },
      });

    getData().then(response => {
      // console.log("::: searchMrp.then ::: ",response.data.gridRowJson);
      // var length = response.data.gridRowJson.length;
      // for(var dataLength=0; dataLength<length; dataLength++){
      //   mrpNo.push(response.data.gridRowJson[dataLength].mrpNo);
      //   mrpNoAndItemCodeList[response.data.gridRowJson[dataLength].mrpNo]=response.data.gridRowJson[dataLength].itemCode;
      //   }
      // console.log(JSON.stringify(mrpNoAndItemCodeList));
      setRowData(response.data.gridRowJson);
    }).catch( e => {
        console.log("searchMrp_getMrpList_error: ", e);
      });
  };
  
  const radioChange = e => {
    console.log("e",e.target.value);
    if(e.target.value==="now"){ 
      setDateVisible(false); 
      var today = new Date();
      var month = today.getMonth()+1;
      var day = today.getDate();
      var date;
      if(month<10) month="0"+month;
      if(day<10) day="0"+day;
      date = today.getFullYear() + '-' + month + '-' + day;
      setSearchDate(date);
    }
    else{    
      setDateVisible(true); 
    }
  }

  const resister = async (e) => {
    for(let i=0; i<dialogData.length; i++){
      delete dialogData[i].errorCode
      delete dialogData[i].errorMsg
      delete dialogData[i].chk
      delete dialogData[i].itemName
    };
    // for(let i=0; i<dialogData.length; i++){
    //   let claimDate = dialogData[i].claimDate.split("/");
    //   let dueDate = dialogData[i].dueDate.split("/");
    //   let newDate = claimDate[0]+"-"+claimDate[1]+"-"+claimDate[2];
    //   let newDate2 =dueDate[0]+"-"+dueDate[1]+"-"+dueDate[2];
    //   dialogData[i].claimDate=newDate;
    //   dialogData[i].dueDate=newDate2;
    // };
    console.log("::: searchDate :::",searchDate);
    console.log("::: batchList :::",dialogData);
    console.log("::: mrpNoAndItemCodeList :::",mrpNoAndItemCodeList);
    console.log("::: mrpNoList :::",mrpNoList);

    const confirmMsg =  "  :  "+ mrpNoList.length +"  \r\n" + 
                        "  : " + searchDate + "\r\n" +
                        "      " + dialogData.length + 
                        "  . \r\n?";
    const confirmStatus = window.confirm(confirmMsg);
    if( confirmStatus === true ) {
      try {
        await axios.post(
          'http://localhost:8282/logi/logistics/production/registerMrpGathering.do',
          { batchList : dialogData, //    
            mrpGatheringRegisterDate : searchDate, //
            mrpNoAndItemCodeList : mrpNoAndItemCodeList },//mrpNo() : ItemCode  }
        ).then( response => {
          console.log("::: response :::",response);
            //  let dataLength=response.data.gridRowJson.length;
          })
          .catch(function (error) {
            console.log(error);
          })
          .finally(function () {
            setDialogData("");
          });
      } catch (e) {
        console.log(e);
      };
    } else {
      alert(" .");
    };
    setOpenDialog(false);
  }

  const searchResult = async () => {
    setOpenDialog(true);
    let mrpNoAndItemCodeLists={};
    const mrpNoList = rowData.map( e => e.mrpNo);
    rowData.map( e => (mrpNoAndItemCodeLists[e.mrpNo]=e.itemCode));
    setMrpNoAndItemCodeList(mrpNoAndItemCodeLists);
    setMrpNoList(mrpNoList);
    console.log("::: rowData-  ::: ",rowData);
    // console.log("::: mrpNoAndItemCodeList ::: ",mrpNoAndItemCodeLists);
    // console.log("::: mrpNoList ::: ",mrpNoList);
    
    try {
      await axios.put(
        'http://localhost:8282/logi/logistics/production/getMrpGatheringList.do', {
        mrpNoList: mrpNoList,
      })
      .then( response =>{
        console.log(':: gridRowJson ::',response.data.gridRowJson);
        setDialogData(response.data.gridRowJson);
      })
      .catch(function (error) {
        console.log(error);
      })
      .finally(function () {
        // always executed
      });
    } catch (err) {
      console.log(err);
    } 
  }

  const closeDialog = e => {
    console.log("e",e);
    setOpenDialog(false);
  }

  return (
    <>
      <div className="contract_header">
        &nbsp;&nbsp;
        <Button
          className={classes.btnSearch}
          variant="contained"
          color="secondary"
          size= "medium"
          onClick={searchResult}
        >   </Button>
      </div>
      <br />
      <hr className={classes.hrStyle} />
      <div className="contract_body">
        <br />
        <Typography className={classes.name}>    </Typography>
        <br />
        <div className={"ag-theme-balham-dark"} style={gridFrameStyle}>
          <AgGridReact
            onGridReady={gridReady}
            columnDefs={headerName}
            style={gridStyle}
            rowSelection={single}
            rowData={rowData}
          />
        </div>
      </div>
      <div>
          <Dialog open={openDialog} onClose={closeDialog}
          maxWidth='650px'>
              <DialogTitle id="simple-dialog-title">  </DialogTitle>
            <tr>
              <td>
        <RadioGroup
            row
            aria-label="position"
          >
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <fieldset className={classes.dialog} >
              <legend>
                <strong>   </strong>
              </legend>
              <FormControlLabel
                value="now"
                control={<Radio />}
                label=""
                onChange={radioChange}
              />
              <FormControlLabel
                value="search"
                control={<Radio />}
                label=""
                onChange={radioChange}
              />
        </fieldset>
      </RadioGroup>
            </td>
            <td>
            <fieldset className={classes.dialog}>
          <legend>
            <strong>    ( MRP )   </strong>
            </legend>
            <Button
                size= "large"
                color="grey"
                className={classes.btnDialogSearch}
                onClick={resister}
              >   </Button>  
            </fieldset>
            </td>
            </tr>

            <form hidden={!dateVisible}>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <TextField
                id={"searchDate"}
                type="date"
                value={searchDate}
                onChange={searchDateChange}
              ></TextField>
            </form>
              <DialogContent>
                  <List>
                      { <div className={"ag-theme-balham"}
                          style={{ height: "500px", width: "700px", paddingTop: "8px" }}>
                          <AgGridReact
                              columnDefs={mrpGatheringGrid}
                              rowData={dialogData}   //  data
                              rowSelection='single'  //   .
                              onGridReady={onModaGridReady}
                              getRowStyle={function () {
                                  return { "text-align": "center" };
                              }}
                          />
                            </div> }
                  </List>
              </DialogContent>
              <DialogActions>
                  <Button variant="outlined" color="secondary"  onClick={closeDialog} >
                  </Button>
              </DialogActions>
            </Dialog>
                             

      </div>
    </>
  );
};

export default MrpSearch;
