import React, { useState, useEffect, useCallback } from "react";
import { AgGridReact } from "ag-grid-react";
import axios from "axios";
import Select from "react-select";
import useSelectItem from "./useSelectItem";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
import Button from "@material-ui/core/Button";
import { FormControl,AppBar,Toolbar,Typography } from "@material-ui/core";
import "./Grid.css";

const MonthSalaryManage = () => {
  const set1 = useSelectItem();
  console.log(" ", set1.selectedmonth);

  // monthList db   selectedmonth  Creatable  state
  const [monthList, setMonthList] = useState([]);
  const [date, setDate] = useState("");
  const [DeptCode, setDeptCode] = useState("");
  const [gridEvent, setGridEvent] = useState();



  function click1() {
    axios
      .get(
        "http://localhost:8282/hr/salary/findCloseSalary.do",
        {
          params: {
            applyYearMonth: date,
            deptCode:DeptCode
          },
        },
      )
      .then(response => {
        console.log(" ", response);
        setMonthList(response.data.monthSalary.result);
      })
      .catch(e => {
        console.log(e);
      });
  }
  //  alert
  
//     
  function currencyFormatter(params) {
    return formatNumber(params.value) + " ";
}

function formatNumber(number) {
    // this puts commas into the number eg 1000 goes to 1,000,
    // i pulled this from stack overflow, i have no idea how it works
    return Math.floor(number).toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}
//     

  //  
  const state = {
    columnDefs: [
      {
        headerName: "",
        field: "empCode",
        sortable: true,
        filter: true,
        resizable: true,
        
      },
      { headerName: "", field: "deptCode" , hide: true},
      { headerName: "", field: "applyYearMonth" , hide: true},
      { headerName: " ", field: "salary" , valueFormatter: currencyFormatter },
      { headerName: "", field: "unusedDaySalary" , valueFormatter: currencyFormatter  },
      { headerName: "", field: "cost" , valueFormatter: currencyFormatter  },
      { headerName: " ", field: "totalExtSal" , valueFormatter: currencyFormatter  },
      { headerName: " ", field: "totalDeduction" , valueFormatter: currencyFormatter  },
      { headerName: "", field: "realSalary" , valueFormatter: currencyFormatter  },
      { headerName: "", field: "totalPayment" , hide: true},
      { headerName: "", field: "finalizeStatus" },

    ],
    rowData: monthList,
  };

  const DPThandleChange = (newValue) => {setDeptCode(newValue.value)};
  const DatehandleChange = (newValue) => {setDate(newValue.value);};


  const customStyles = {
    menu: (provided, state) => ({
      ...provided,
      width: state.selectProps.width,
      borderBottom: "1px dotted pink",
      color: state.selectProps.menuColor,
      padding: 20,
    }),
    singleValue: (provided, state) => {
      const opacity = state.isDisabled ? 0.5 : 1;
      const transition = "opacity 300ms";

      return { ...provided, opacity, transition };
    },
  };

  return (
    console.log("", monthList),
    (
      <div>
          <div>
          <AppBar position="relative">
            <Toolbar>
              <Typography variant="h5"> </Typography>
            </Toolbar>
          </AppBar>
        </div>
        <br />
        <div>
        <FormControl style={{ minWidth: "410px" }}></FormControl>
        <FormControl style={{ minWidth: "200px" }}>
        <Select
              styles={customStyles}
              onChange={DPThandleChange}
              options={set1.selectDeptCode}
              placeholder=" "
            >
            </Select>
        </FormControl>
          <FormControl style={{ minWidth: "250px" }}>
            <Select
              styles={customStyles}
              onChange={DatehandleChange}
              options={set1.selectedmonth}
              placeholder=" "
            >
            </Select>               
          </FormControl>
            <FormControl style={{ minWidth: "50px" }}>
                        <Button variant="contained" color="primary" onClick={click1}>
                        
                        </Button>
            </FormControl>

        </div>
        <br />
        <div
          className="ag-theme-balham"
          style={{
            height: "600px",
            width: "1300px",
            textAlign:"center"
          }}
        >
          <AgGridReact
            columnDefs={state.columnDefs}
            rowData={state.rowData}
            //rowSelection="multiple"
            
            onGridReady={event => {
              event.api.sizeColumnsToFit();
              setGridEvent(event);
            }}
          ></AgGridReact>
        </div>
      </div>
    )
  );
};
export default MonthSalaryManage;

