import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  List,
  DialogActions,
  Button,
  TextField,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import { AgGridReact } from "ag-grid-react";
import { SEARCH_ACCOUNT_LIST_REQUEST } from "../../ActionType/ActionType";
import useStyles from "./Theme";

const AccountSearchDialog = ({ open, close }) => {
  const [positionGridApi, setPositionGridApi] = useState([]);
  const data = useSelector(({ AccReducer }) => AccReducer.accountList, []); //DB     
  const dispatch = useDispatch();
  const classes = useStyles();

  const accountColumnDefs = [
    { headerName: " ", field: "accountInnerCode", width: 180 },
    { headerName: "   ", field: "accountName", width: 210 },
  ];

  const handleClose = () => {
    close({
      data: positionGridApi.getSelectedRows(), // data  row ,
      division: "accountDialog",
    });
  };

  const Close = () => {
    close({
      division: "accountDialog",
    });
  };

  const [accountName, setAccountName] = useState(""); // 
  const [accountCode, setAccountCode] = useState(""); // 

  //onChange 
  const onChange = e => {
    let searchCode = e.target.value;
    console.log("isNaN(searchCode)", isNaN(searchCode)); //  
    if (isNaN(searchCode)) {
      setAccountName(searchCode);
      setAccountCode("undefined");
    } else {
      setAccountName("undefined");
      setAccountCode(searchCode);
    }
  };

  //    
  const selectData = () => {
    dispatch({
      type: SEARCH_ACCOUNT_LIST_REQUEST,
      params: {
        accountName: accountName,
        accountCode: accountCode,
      },
    });
  };

  const handleKeyPress = e => {
    if (e.key === "Enter") {
      selectData();
    }
  };

  return (
    <Dialog
      aria-labelledby="simple-dialog-title"
      open={open}
      fullWidth={true}
      maxWidth={"xs"}
    >
      <DialogTitle id="simple-dialog-title" Align="center">
           
      </DialogTitle>
      <DialogContent dividers>
        <List>
          <div Align="center" className={classes.margin}>
            <TextField
              margin="normal"
              placeholder=""
              onChange={onChange}
              onKeyPress={handleKeyPress}
            />
            <Button
              variant="contained"
              //size="large"
              color="primary"
              startIcon={<SearchIcon />} //
              onClick={selectData}
            >
              
            </Button>
          </div>
          <div
            className={"ag-theme-material"} // 
            style={{
              height: "300px",
              width: "100%",
              paddingTop: "8px",
            }}
          >
            <AgGridReact
              columnDefs={accountColumnDefs} //
              rowData={data} //  data
              rowSelection="single" //   .
              getRowStyle={param => {
                return { "text-align": "center" }; //body  
              }}
              onGridReady={params => setPositionGridApi(params.api)}
              onCellClicked={handleClose} // cell , handleClose .
            />
          </div>
        </List>
      </DialogContent>
      <DialogActions>
        <Button onClick={Close} color="primary">
          Close
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AccountSearchDialog;
